import React, { useContext, useEffect } from "react";
import Button from "@material-ui/core/Button";
import CssBaseline from "@material-ui/core/CssBaseline";
import TextField from "@material-ui/core/TextField";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import Link from "@material-ui/core/Link";
import Grid from "@material-ui/core/Grid";
import Box from "@material-ui/core/Box";
import Typography from "@material-ui/core/Typography";
import useStyles from "./Styles";
import Container from "@material-ui/core/Container";
import { useForm } from "react-hook-form";
import Logo from "../../Components/Logo.js";
import ApiService, { generateCancelToken } from "../../Service/apiService";
import { useHistory } from "react-router";
import { AuthContext } from "@globalContext/AuthContext";

function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {"Copyright  "}
      <Link color="inherit" href="https://material-ui.com/">
        Coders.Id
      </Link>{" "}
      {new Date().getFullYear()}
      {"."}
    </Typography>
  );
}

const apiCancelToken = generateCancelToken();
const api = new ApiService(apiCancelToken);

export default function SignUpPage() {
  const classes = useStyles();
  const login = useContext(AuthContext);
  const route = useHistory();
  const { register, getValues, errors } = useForm({
    mode: "onChange",
    reValidateMode: "onChange",
    defaultValues: {
      firstName: "",
      lastName: "",
      email: "",
      password: "",
      passwordConfirmation: "",
      remember: false,
    },
  });

  const onRegisterSuccess = (response) => console.log(response);
  const onRegisterFailed = (error) => console.log(error);

  const handleOnSignUp = () => {
    let dataUser = {
      fullName: `${getValues().firstName} ${getValues().lastName}`,
      userName: `${getValues().firstName}${getValues().lastName}${Math.floor(
        Math.random() * 100
      )}`,
      email: getValues().email,
      password: getValues().password,
      password_confirmation: getValues().passwordConfirmation,
    };
    if (Object.keys(errors).length === 0) {
      api.getUserCookie()
        .then(() => {
          api.saveNewUser(dataUser).then(onRegisterSuccess).catch(onRegisterFailed);
        })
        .catch(onRegisterFailed);
    }
  };

  useEffect(() => {
    if (login.isLoggedIn) {
      route.push('/dashbox');
    }

    return () => {
      apiCancelToken.cancel();
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <Logo />
        <Typography component="h1" variant="h5">
          Sign up for CodersBox
        </Typography>
        <form className={classes.form} method="post">
          <Grid container spacing={2}>
            <Grid item xs={12} sm={6}>
              <TextField
                autoComplete="fname"
                name="firstName"
                variant="outlined"
                required
                fullWidth
                id="firstName"
                label="First Name"
                autoFocus
                inputRef={register({
                  required: "You must provide your first name!",
                  pattern: {
                    message: "You must provide your first name",
                  },
                })}
                error={!!errors.firstName}
                className={classes.margin}
              />
              {errors.firstName && (
                <span className={classes.error}>
                  {errors.firstName.message}
                </span>
              )}
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                variant="outlined"
                required
                fullWidth
                id="lastName"
                label="Last Name"
                name="lastName"
                autoComplete="lname"
                inputRef={register({
                  required: "You must provide your last name!",
                  pattern: {
                    message: "You must provide your last name",
                  },
                })}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                variant="outlined"
                margin="normal"
                required
                fullWidth
                id="email"
                label="Email Address"
                name="email"
                autoComplete="email"
                autoFocus
                inputRef={register({
                  required: "You must provide the email address!",
                  pattern: {
                    value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                    message: "You must provide a valid email address!",
                  },
                })}
                error={!!errors.email}
                className={classes.margin}
              />
              {errors.email && (
                <span className={classes.error}>{errors.email.message}</span>
              )}
            </Grid>
            <Grid item xs={12}>
              <TextField
                variant="outlined"
                margin="normal"
                required
                fullWidth
                name="password"
                label="Password"
                type="password"
                id="password"
                autoComplete="current-password"
                inputRef={register({
                  required: "You must provide a password.",
                  minLength: {
                    value: 8,
                    message: "Your password must be greater than 8 characters",
                  },
                })}
                error={!!errors.password}
              />
              {errors.password && (
                <span className={classes.error}>{errors.password.message}</span>
              )}
            </Grid>
            <Grid item xs={12}>
              <TextField
                variant="outlined"
                margin="normal"
                required
                fullWidth
                name="passwordConfirmation"
                label="PasswordConfirmation"
                type="password"
                id="passwordConfirmation"
                autoComplete="current-password"
                inputRef={register({
                  required: "Your Password Mismatch",
                  minLength: {
                    value: "password",
                    message: "Your Password Mismatch",
                  },
                })}
                error={!!errors.passwordConfirmation}
              />
              {errors.password && (
                <span className={classes.error}>
                  {errors.passwordConfirmation?.message}
                </span>
              )}
            </Grid>
            <Grid item xs={12}>
              <FormControlLabel
                control={<Checkbox value="allowExtraEmails" color="primary" />}
                label="I want to receive inspiration, marketing promotions and updates via email."
              />
            </Grid>
          </Grid>
          <Button
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
            onClick={() => handleOnSignUp()}
          >
            Sign Up
          </Button>
          <Grid container justify="flex-end">
            <Grid item>
              <Link href="/login" variant="body2">
                Already have an account? Sign in
              </Link>
            </Grid>
          </Grid>
        </form>
      </div>
      <Box mt={5}>
        <Copyright />
      </Box>
    </Container>
  );
}
