import { FormControl, Grid, TextField, Select, InputLabel, MenuItem } from '@material-ui/core';
import { useBoxState } from '@pages/BoxPage/Context/boxContextTracked';
import { useWidgetState } from '@pages/BoxPage/Context/widgetContextTracked';
import { resetWidgetDataValueArray, resetWidgetOptionArray, setWidgetArrayDataValue, setWidgetDataSource, setWidgetOption } from '@pages/BoxPage/Redux/WidgetSlice';
import { setWidgetUIuseOwnDataField } from '@pages/BoxPage/Redux/WidgetUISlice';
import { useAppDispatch, useTypedReduxSelector } from '@store/Store';
import React, { useEffect, useState } from 'react';


/**
 * Widget Form for label. Single or multiple
 * 
 * 
 * @param {any} props 
 * @returns {JSX} React Element
 */
export default function MapWidgetForm(props) {
  const dispatch = useAppDispatch();
  const boxState = useTypedReduxSelector(state => state.box);
  const widgetState = useTypedReduxSelector(state => state.widget);

  const handleDataSourceSelection = (e) => {
    dispatch(setWidgetDataSource(e.target.value));
  }

  const handleDataValue = (value, index) => {
    dispatch(setWidgetArrayDataValue({
      index: index,
      value: value,
    }));
  }

  useEffect(() => {
    dispatch(setWidgetDataSource("datafield"));
    dispatch(resetWidgetDataValueArray());
    dispatch(resetWidgetOptionArray({optionName: 'label'}));
    dispatch(setWidgetOption({
      isMultiSeries: false,
    }));
    dispatch(setWidgetUIuseOwnDataField(true));

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <React.Fragment>
      <Grid item xs={12} md={12} lg={12}>
        <FormControl variant="outlined" style={{ minWidth: 220, width: '100%' }}>
          <InputLabel id="data-source-selection-input">Coordinate Data Source</InputLabel>
          <Select
            labelId="data-source-selection"
            id="data-source-selection"
            onChange={handleDataSourceSelection}
            label="Coordinate Data Source"
          >
            <MenuItem value={null}>
              <em>None</em>
            </MenuItem>
            <MenuItem value={'manual'}>
              <em>Manual</em>
            </MenuItem>
            <MenuItem value={'datafield'}>
              <em>Data Field</em>
            </MenuItem>

          </Select>
        </FormControl>
      </Grid>
      <Grid item xs={6} md={6} lg={6}>
        <FormControl disabled={widgetState.data?.source === undefined} required variant="outlined" style={{ minWidth: 220, display: widgetState.data?.source === 'datafield' ? null : 'none' }}>
          <InputLabel id="widget-data-field-value">Longitude Field Name</InputLabel>
          <Select
            labelId="field-value-type"
            id="demo-simple-select-outlined"
            onChange={event => handleDataValue(event.target.value, 0)}
            label="Longitude Field Name"
          >
            {
              boxState.boxSettings.dataField?.length !== 0 ?
                boxState.boxSettings.dataField.map((field, i) => (
                  <MenuItem key={i} value={field}>{field}</MenuItem>
                ))
                : <MenuItem value={0}><em>None</em></MenuItem>
            }
          </Select>
        </FormControl>
        <FormControl variant="outlined" style={{ minWidth: 220, display: widgetState.data?.source === 'manual' ? null : 'none' }}>
          <TextField onBlur={event => handleDataValue(event.target.value, 1)} variant="outlined" id="widget-data-manual-value" label="Longitude Manual Value" />
        </FormControl>
      </Grid>
      <Grid item xs={6} md={6} lg={6}>
        <FormControl required variant="outlined" style={{ minWidth: 220, display: widgetState.data?.source === 'datafield' ? null : 'none' }}>
          <InputLabel id="widget-data-field-value">Latitude Field Name</InputLabel>
          <Select
            labelId="field-value-type"
            id="demo-simple-select-outlined"
            onChange={event => handleDataValue(event.target.value, 1)}
            label="Latitude Field Name"
          >
            {
              boxState.boxSettings.dataField?.length !== 0 ?
                boxState.boxSettings.dataField.map((field, i) => (
                  <MenuItem key={i} value={field}>{field}</MenuItem>
                ))
                : <MenuItem value={0}><em>None</em></MenuItem>
            }
          </Select>
        </FormControl>
        <FormControl variant="outlined" style={{ minWidth: 220, display: widgetState.data?.source === 'manual' ? null : 'none' }}>
          <TextField onBlur={event => handleDataValue(event.target.value, 1)} variant="outlined" id="widget-data-manual-value" label="Latitude Manual Value" />
        </FormControl>
      </Grid>
    </React.Fragment>
  );
}