import { useAppStateValue } from '@globalContext/AppContextTracked';
import { Button, CircularProgress, FormControlLabel, Grid, Switch, Tooltip, Typography, useTheme } from '@material-ui/core';
import AddIcons from '@material-ui/icons/AddBox';
import SettingBox from '@material-ui/icons/SettingsApplications';
import { setAddNewWidget, setBoxEditMode } from '@store/AppSlice';
import { useAppDispatch, useTypedReduxSelector } from '@store/Store';
import React, { useEffect } from 'react';
import { Link, useRouteMatch } from 'react-router-dom';
import { useBoxState } from '../Context/boxContextTracked';
import { useWidgetSetState } from '../Context/widgetContextTracked';
import { setBoxUIAddNewWidget, setBoxUIEditMode } from '../Redux/BoxUISlice';
import Loading from './loadingBar';

export default function BoxHeader() {
  const dispatch = useAppDispatch();
  const boxState = useTypedReduxSelector(state => state.box);
  const appState = useTypedReduxSelector(state => state.app);
  const boxUIState = useTypedReduxSelector(state => state.boxPageUI);
  const { url } = useRouteMatch();
  const theme = useTheme();


  const onAddWidgetClick = () => {
    dispatch(
      setBoxUIAddNewWidget(true)
    );
  }

  return (
    <React.Fragment>
      <Grid item xs={12} md={6} lg={6}>
        <input type="hidden" value={boxState.boxToken} name="boxToken" id="boxToken" />
        <Typography variant="h4" component="h2">
          {boxState.boxName === '' ? (<Loading width={400} height={40} />) : boxState.boxName}
        </Typography>
        <Typography variant="subtitle1" component="h4" color="textSecondary">
          {boxState.boxTagName === '' ? (<Loading width={300} height={20} />) : boxState.boxTagName}
        </Typography>
      </Grid>
      <Grid item xs={12} md={6} lg={6}>
        <Grid container direction="row" justify="flex-end" alignItems="center" spacing={1}>
          <Grid item>
            <Button
              variant="contained"
              style={{ display: boxUIState.isBoxEditMode ? null : 'none' }}
              color="primary"
              startIcon={<AddIcons fontSize="large" />}
              onClick={onAddWidgetClick} aria-label="Add new widget"
            >
              Add Widget
            </Button>
          </Grid>
          <Grid item>
            <Button
              variant="contained"
              style={{ backgroundColor: theme.palette.success.main, color: 'white', display: boxUIState.isBoxEditMode ? null : 'none' }}
              startIcon={<SettingBox fontSize="large" />}
              component={Link}
              to={`${url}/settings`}
              aria-label="Box settings"
            >
              Settings
            </Button>
          </Grid>
          <Grid item>
            <CircularProgress thickness={6} size={30} style={{ display: appState.fetchingStatus ? null : 'none' }} />
          </Grid>
          <Grid item>
            <Tooltip title={boxUIState.isBoxEditMode ? "Goes to Online mode" : "Goes to Edit mode. Arrange your widget here"}>
              <FormControlLabel
                control={
                  <Switch checked={boxUIState.isBoxEditMode}
                    onChange={() => dispatch(setBoxUIEditMode(!boxUIState.isBoxEditMode))}
                    name="checkedB"
                    color="primary" />
                }
              />
            </Tooltip>
          </Grid>
        </Grid>
      </Grid>
    </React.Fragment>
  );
}