import { Divider, Drawer, Grid, IconButton, List, ListItem, ListItemIcon, ListItemText, Typography, useMediaQuery, useTheme } from '@material-ui/core';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import { setDrawerState } from '@store/AppSlice';
import { useAppDispatch, useTypedReduxSelector } from '@store/Store';
import React, { useEffect } from 'react';
import { Link } from 'react-router-dom';
import { GetRouteGroup, routes } from '../Route';
import Logo from './Logo';
import useStyles from './Styles';

export function ListItemLink(props) {
  const classes = useStyles();

  return (
    <ListItem className={classes.drawerList} button component={Link} {...props} />
  );
}

export default function DrawerMenu(props) {
  const dispatch = useAppDispatch();
  const appState = useTypedReduxSelector(state => state.app);
  const classes = useStyles();
  const theme = useTheme();
  const routeGroup = GetRouteGroup();
  const mobile = useMediaQuery(theme.breakpoints.down('sm'));

  useEffect(() => {
    if(mobile) {
      dispatch(setDrawerState(false));
    } else {
      dispatch(setDrawerState(true));
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [mobile]);

  return (
    <Drawer
      className={classes.drawer}
      variant="persistent"
      anchor="left"
      open={appState.isDrawerOpen}
      classes={{
        paper: classes.drawerPaper,
      }}
    >
      <div className={classes.drawerToolbar}>
        <Grid container direction="row" justify="space-between" alignItems="center">
          <Grid item>
            <Logo logoType="boxicon" logoSize="small" />
          </Grid>
          <Grid item>
            <IconButton color="secondary" onClick={() => dispatch(setDrawerState(!appState.isDrawerOpen))}>
              {theme.direction === 'rtl' ? <ChevronRightIcon /> : <ChevronLeftIcon />}
            </IconButton>
          </Grid>
        </Grid>
      </div>
      {/* <Divider /> */}
      {/** Menu Goes here */}
      {routeGroup.map((value, index) => (
        <React.Fragment key={index}>
          <Divider className={classes.drawerDivider} />
          <Typography className={classes.drawerDividerTitle} variant="h6">{value}</Typography>
          <List className={classes.drawerListContainer}>
            {routes.map((route, index) =>
              route.isDrawerMenu && route.group === value ?
                (<ListItemLink key={index} to={route.path}>
                  <ListItemIcon>
                    <route.icon />
                  </ListItemIcon>
                  <ListItemText primary={route.name} />
                </ListItemLink>) : null
            )}
          </List>
        </React.Fragment>
      ))}

    </Drawer>
  );
}