import { useAppState } from '@globalContext/AppContextTracked';
import { Grid } from '@material-ui/core';
import { setBoxUIShouldRefresh } from '@pages/BoxPage/Redux/BoxUISlice';
import { resetWidgetUILayout, setWidgetUILayout } from '@pages/BoxPage/Redux/WidgetUISlice';
import ApiService, { generateCancelToken } from '@services/apiService';
import { setFetchingState, setFetchingStatus } from '@store/AppSlice';
import { useAppDispatch, useTypedReduxSelector } from '@store/Store';
import React, { useEffect } from 'react';
import { useBoxState } from '../../Context/boxContextTracked';
import { useWidgetState } from '../../Context/widgetContextTracked';
import BoxHeader from '../boxHeader';
import NoWidgetFound from './NoWidgetFound';
import WidgetResponsiveGrid from './WidgetResponsiveGrid';

/**
 * Loading all widget on box happens here.
 * 
 * 
 * @returns JSX
 */
export default function WidgetContainer() {
  const dispatch = useAppDispatch();
  const boxState = useTypedReduxSelector(state => state.box);
  const boxUIState = useTypedReduxSelector(state => state.boxPageUI);
  const widgetUIState = useTypedReduxSelector(state => state.widgetUI);
  const widgetState = useTypedReduxSelector(state => state.widget);
  const apiCancelToken = generateCancelToken();
  const api = new ApiService(apiCancelToken);


  /**
   * Update and save into database
   * widget layout that user made
   * 
   * 
   * @returns Promise
   */
  const updateWidgetLayout = async () => {

    if (boxState.boxToken === '' && localStorage.getItem(`boxLayout-${boxState.boxToken}`) === null) {
      return Promise.reject();
    }

    try {
      let dataUpdate = {
        boxToken: boxState.boxToken,
        widgetUniqueId: widgetState.widgetId,
        widgetLayout: localStorage.getItem(`boxLayout-${boxState.boxToken}`) || JSON.stringify({}),
        widgetBoxLayout: localStorage.getItem(`boxLayout-${boxState.boxToken}-allLayouts`) || JSON.stringify({}),
      }
      dispatch(setFetchingStatus(true));
      const updateLayout = await api.updateWidgetBoxLayout(dataUpdate, apiCancelToken);
      if (updateLayout.data.header.status === 'success') {
        dispatch(
          setFetchingState({
            message: 'layout has been saved',
            severity: 'success',
            isOpen: true,
            fetchingStatus: false,
          })
        );
        dispatch(setBoxUIShouldRefresh(true));

      }
    } catch (error) {
      dispatch(
        setFetchingState({
          message: 'Something went wrong, please try again!',
          severity: 'error',
          isOpen: true,
          fetchingStatus: false,
        })
      );
      console.error(error);
    }
  }

  /**
   *  --------------
   *  Set boxWidget state
   *  --------------
   */
  useEffect(() => {
    if (widgetUIState.widgetsLayout.length !== 0) {
      dispatch(resetWidgetUILayout());
    }

    boxState.boxWidgets.map((widget) => {
      dispatch(setWidgetUILayout(widget.layout));
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [boxState.boxWidgets]);

  /**
   *  --------------
   *  Save layout into database when switch is false
   *  --------------
   */
  useEffect(() => {
    if (!boxUIState.isBoxEditMode) {
      updateWidgetLayout();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [boxUIState.isBoxEditMode]);


  /**
   *  --------------
   *  Cleaning up
   *  --------------
   */
  useEffect(() => {
    return () => {
      apiCancelToken.cancel("Widget Container");
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <React.Fragment>
      <Grid container spacing={1}>
        <BoxHeader />
      </Grid>
      {
        boxState.boxWidgets.length !== 0 && widgetUIState.widgetsLayout.length !== 0 ?
          (
            <WidgetResponsiveGrid initialLayout={widgetUIState.widgetsLayout} />
          )
          : <NoWidgetFound />
      }
    </React.Fragment>
  )
}