import { createSlice, PayloadAction } from "@reduxjs/toolkit";

export interface BoxUINewWidgetChoose {
  state: boolean;
  name: string;
}

export interface BoxUIInitialState {
  isBoxEditMode: boolean;
  isBoxEditModePrev: boolean;
  isEditWidget: boolean;
  isAddNewWidget: boolean;
  shouldBoxRefresh: boolean;
  isNewWidgetChoosed: BoxUINewWidgetChoose;
}

export const initialState: BoxUIInitialState = {
  isBoxEditMode: false,
  isBoxEditModePrev: false,
  isEditWidget: false,
  isAddNewWidget: false,
  shouldBoxRefresh: false,
  isNewWidgetChoosed: {
    state: false,
    name: '',
  },
}

const BoxUISlice = createSlice({
  name: 'BoxUISlice',
  initialState: initialState,
  reducers: {
    setBoxUIEditMode: (state, action: PayloadAction<boolean>) => {
      state.isBoxEditModePrev = state.isBoxEditMode;
      state.isBoxEditMode = action.payload;
    },
    setBoxUIEditWidget: (state, action: PayloadAction<boolean>) => {
      state.isEditWidget = action.payload;
    },
    setBoxUIAddNewWidget: (state, action: PayloadAction<boolean>) => {
      state.isAddNewWidget = action.payload;
    },
    setBoxUIShouldRefresh: (state, action: PayloadAction<boolean>) => {
      state.shouldBoxRefresh = action.payload;
    },
    setBoxUINewWidgetChoosed: (state, action: PayloadAction<BoxUINewWidgetChoose>) => {
      state.isNewWidgetChoosed.name = action.payload.name;
      state.isNewWidgetChoosed.state = action.payload.state;
    },
  }
});


export const {
  setBoxUIAddNewWidget, 
  setBoxUIEditMode, 
  setBoxUIEditWidget,
  setBoxUIShouldRefresh,
  setBoxUINewWidgetChoosed
} = BoxUISlice.actions;
export default BoxUISlice.reducer;

