import { AuthContext } from '@globalContext/AuthContext';
import { Avatar, Card, CardActions, CardContent, CardHeader, Grid, TextField, Typography } from '@material-ui/core';
import React, { useContext } from 'react';
import useStyles from '../Styles';

export default function FormProfile() {
  const classes = useStyles();
  const auth = useContext(AuthContext);

  return (
    <React.Fragment>
      <Grid item lg={4} md={4} xs={12}>
        <Typography variant="h5" >
          Profile
          </Typography>
        <Typography variant="subtitle1" component="h4" color="textSecondary" gutterBottom>
          Profile Information
          </Typography>
      </Grid>
      <Grid item lg={8} md={8} xs={12}>
        <Card>
          <CardContent className={classes.accountFieldContainer}>
            <Typography component="h1" variant="subtitle1" color="textSecondary">
              Email
              </Typography>
            <CardHeader
              avatar={
                <Avatar aria-label="username">
                  DB
                  </Avatar>
              }
              title={auth.userName}
              subheader={auth.userEmail}
            />
            <br />
            <TextField disabled value={auth.userName} variant="outlined" id="username" label="Username" />
          </CardContent>
          <CardActions className={classes.accountCardAction}>
            {/*  */}
          </CardActions>
        </Card>
      </Grid>
    </React.Fragment>
  )
}