import { useAppState } from '@globalContext/AppContextTracked';
import {
  Button, CircularProgress, Divider, FormControl, Grid, InputLabel, MenuItem, Select, TextField, Tooltip
} from '@material-ui/core';
import SaveIcon from '@material-ui/icons/Save';
import { useBoxState } from '@pages/BoxPage/Context/boxContextTracked';
import { useWidgetState } from '@pages/BoxPage/Context/widgetContextTracked';
import { resetWidgetSlice, setWidgetArrayDataValue, setWidgetDataSource, setWidgetDataValue, setWidgetOption, setWidgetProperties, setWidgetSubtitle, setWidgetTitle } from '@pages/BoxPage/Redux/WidgetSlice';
import ApiService, { generateCancelToken } from '@services/apiService';
import { setFetchingState, setFetchingStatus } from '@store/AppSlice';
import { useAppDispatch, useTypedReduxSelector } from '@store/Store';
import React, { useEffect, useState } from 'react';
import { SketchPicker } from 'react-color';
import useStyles from '../Styles';

const apiCancelToken = generateCancelToken();
const api = new ApiService(apiCancelToken);

export default function EditWidgetForm() {
  const dispatch = useAppDispatch();
  const appState = useTypedReduxSelector(state => state.app);
  const boxState = useTypedReduxSelector(state => state.box);
  const widgetState = useTypedReduxSelector(state => state.widget);
  const [isPickerShow, setPickerShow] = useState(false);

  const classes = useStyles();

  const handleWidgetTitle = (e) => {
    dispatch(setWidgetTitle(e.target.value));
  }

  const handleWidgetSubTitle = (e) => {
    dispatch(setWidgetSubtitle(e.target.value));

  }

  const handleDataSourceSelection = (e) => {
    dispatch(setWidgetDataSource(e.target.value));
  }

  const handleDataValue = (e) => {
    dispatch(setWidgetDataValue(e.target.value));
  }

  const handleDataValueArray = (value, index) => {
    dispatch(setWidgetArrayDataValue({
      index: index,
      value: value,
    }));
  }

  const handleColorChange = (color) => {
    dispatch(setWidgetOption({
      color: color.hex,
    }));
  }

  const handleValueChange = (value, key) => {
    dispatch(setWidgetOption({
      [key]: parseInt(value, 10)
    }));
  }

  const handleSaveButton = async () => {
    dispatch(setFetchingStatus(true));
    try {
      let data = {
        boxToken: boxState.boxToken,
        widgetUniqueId: widgetState.widgetId,
        widgetProperties: JSON.stringify(widgetState.properties),
        widgetOptions: JSON.stringify(widgetState.options),
        widgetData: JSON.stringify(widgetState.data),
      }
      const response = await api.updateWidgetBoxSettings(data);

      if (response.data?.header?.status === 'failed') {
        dispatch(setFetchingState({
          message: 'Something wrong while update your widget! no data returned',
          severity: 'error',
          isOpen: true,
          fetchingStatus: false,
        }));
        return;
      }
      dispatch(setFetchingState({
        message: 'Congratulations! your widget has been updated',
        severity: 'success',
        isOpen: true,
        fetchingStatus: false,
      }));
    } catch (error) {
      console.error(error);
      dispatch(setFetchingState({
        message: 'Something wrong while update your widget!',
        severity: 'error',
        isOpen: true,
        fetchingStatus: false,
      }));
    }
  }

  useEffect(() => {
    return () => dispatch(resetWidgetSlice());

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <React.Fragment>
      <form className={classes.fieldFlex}>
        <Grid container spacing={3}>
          <Grid item xs={12} md={12} lg={12}>
            <TextField disabled={appState.fetchingStatus} value={widgetState.properties?.title || 'waiting..'} required onChange={handleWidgetTitle} variant="outlined" id="widget-title" label="Widget Title" aria-describedby="widget-title-error" />
          </Grid>
          <Grid item xs={12} md={12} lg={12}>
            <TextField disabled={appState.fetchingStatus} value={widgetState.properties?.subtitle || 'waiting..'} required onChange={handleWidgetSubTitle} variant="outlined" id="widget-subtitle" label="Widget Subtitle" />
          </Grid>
          <Divider flexItem style={{ marginTop: 20 }} />

          <Grid item xs={12} md={12} lg={12}>
            <FormControl variant="outlined" style={{ width: '100%', minWidth: 220 }}>
              <InputLabel disabled={appState.fetchingStatus} id="data-source-selection-input">Data Source</InputLabel>
              <Select
                disabled={appState.fetchingStatus}
                labelId="data-source-selection"
                id="data-source-selection"
                onChange={handleDataSourceSelection}
                value={widgetState.data?.source || 'none'}
                label="Data Source"
              >
                <MenuItem value={'none'}>
                  <em>None</em>
                </MenuItem>
                <MenuItem value={'manual'}>
                  <em>Manual</em>
                </MenuItem>
                <MenuItem value={'datafield'}>
                  <em>Data Field</em>
                </MenuItem>

              </Select>
            </FormControl>
            {typeof widgetState.data?.value === 'object' ?
              widgetState.data?.value?.map((data, index) => (
                <FormControl required variant="outlined" style={{ width: '100%', marginTop: 20, minWidth: 220, display: widgetState.data?.source === 'datafield' ? null : 'none' }}>
                  <InputLabel disabled={appState.fetchingStatus} id="widget-data-field-value">Field Name</InputLabel>
                  <Select
                    disabled={appState.fetchingStatus}
                    labelId="field-value-type"
                    id="demo-simple-select-outlined"
                    value={widgetState.data?.value[index] || 0}
                    onChange={(event) => handleDataValueArray(event.target.value, index)}
                    label="Field Name"
                  >
                    {
                      boxState.boxSettings.dataField?.length !== 0 ?
                        boxState.boxSettings.dataField.map((field, i) => (
                          <MenuItem key={i} value={field}>{field}</MenuItem>
                        ))
                        : <MenuItem value={0}><em>None</em></MenuItem>
                    }
                  </Select>
                </FormControl>
              )) :
              <FormControl required variant="outlined" style={{ width: '100%', marginTop: 20, minWidth: 220, display: widgetState.data?.source === 'datafield' ? null : 'none' }}>
                <InputLabel disabled={appState.fetchingStatus} id="widget-data-field-value">Field Name</InputLabel>
                <Select
                  disabled={appState.fetchingStatus}
                  labelId="field-value-type"
                  id="demo-simple-select-outlined"
                  value={widgetState.data?.value || 0}
                  onChange={handleDataValue}
                  label="Field Name"
                >
                  {
                    boxState.boxSettings.dataField?.length !== 0 ?
                      boxState.boxSettings.dataField.map((field, i) => (
                        <MenuItem key={i} value={field}>{field}</MenuItem>
                      ))
                      : <MenuItem value={0}><em>None</em></MenuItem>
                  }
                </Select>
              </FormControl>}
            <FormControl variant="outlined" style={{ minWidth: 220, marginTop: 20, width: '100%', display: widgetState.data?.source === 'manual' ? null : 'none' }}>
              <TextField disabled={appState.fetchingStatus} value={widgetState.data?.value || 'waiting...'} onChange={handleDataValue} variant="outlined" id="widget-data-manual-value" label="Data Manual Value" />
            </FormControl>
          </Grid>
          <Grid item xs={12} md={12} lg={12}>
            <TextField onFocus={() => setPickerShow(true)} disabled={appState.fetchingStatus} value={widgetState.options?.color || 'waiting..'} required onChange={handleColorChange} variant="outlined" id="background-color" label="Background Color" aria-describedby="widget-color-error" />
            {isPickerShow && <SketchPicker
              color={widgetState.options?.color}
              onChange={handleColorChange}
              onChangeComplete={() => setPickerShow(false)}
              className={classes.colorPicker}
            />}
          </Grid>
          <Grid item xs={12} md={12} lg={12}>
            <FormControl variant="outlined" style={{ minWidth: 220, width: '100%' }}>
              <Tooltip title={"Minimum limit widget value"} arrow>
                <TextField value={widgetState.options?.minValue === 0? '0' : widgetState.options?.minValue || null} type="number" onChange={(event) => handleValueChange(event.target.value, "minValue")} variant="outlined" id="minimum-value-range" label="Minimum Value Range" InputLabelProps={{ shrink: true }} />
              </Tooltip>
            </FormControl>
            <FormControl variant="outlined" style={{ minWidth: 220, width: '100%', marginTop: 20 }}>
              <Tooltip title={"Maximum limit widget value"} arrow>
                <TextField value={widgetState.options?.maxValue || null} type="number" onChange={(event) => handleValueChange(event.target.value, "maxValue")} variant="outlined" id="maximum-value-range" label="Maximum Value Range" InputLabelProps={{ shrink: true }} />
              </Tooltip>
            </FormControl>
          </Grid>
          <Grid item xs={12} md={12} lg={12}>
            <input type="hidden" value={boxState.shouldBoxRefresh} />
            <Button
              variant="contained"
              color="primary"
              size="large"
              style={{ width: '100%' }}
              startIcon={appState.fetchingStatus ? null : <SaveIcon />}
              disabled={appState.fetchingStatus}
              onClick={handleSaveButton}
            >
              {appState.fetchingStatus ? (<CircularProgress color="primary" />) : 'Save'}
            </Button>
          </Grid>
        </Grid>
      </form>
    </React.Fragment>
  )
}