import { AuthContext } from '@globalContext/AuthContext';
import { Button, Card, CardActions, CardContent, Grid, IconButton, InputAdornment, TextField, Typography } from '@material-ui/core';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import ApiService, { generateCancelToken } from '@services/apiService';
import { setFetchingState, setFetchingStatus } from '@store/AppSlice';
import { useAppDispatch } from '@store/Store';
import React, { useContext, useEffect, useState } from 'react';
import useStyles from '../Styles';

const apiCancelToken = generateCancelToken();
const api = new ApiService(apiCancelToken);

export default function FormTelegram() {
  const classes = useStyles();
  const dispatch = useAppDispatch();
  const auth = useContext(AuthContext);
  const [userTelegramID, setUserTelegramID] = useState(auth.userTelegramID);
  const [visibilityTelegramID, setVisibilityTelegramID] = useState(false);

  const handleUpdateTelegram = async () => {
    try {
      const dataUpdate = {
        telegramID: userTelegramID,
      }
      dispatch(
        setFetchingStatus(true)
      );
      await api.updateUserTelegramID(dataUpdate);
      dispatch(
        setFetchingState({
          message: 'Telegram ID updated successfully',
          isOpen: true,
          severity: 'success',
          fetchingStatus: false,
        })
      );
    } catch (error) {
      console.log(error);
      dispatch(
        setFetchingState({
          message: 'Telegram ID update failed',
          isOpen: true,
          severity: 'error',
          fetchingStatus: false,
        })
      );
    }
  }

  useEffect(() => {
    return () => apiCancelToken.cancel();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <React.Fragment>
      <Grid item lg={4} md={4} xs={12}>
        <Typography variant="h5" >
          Telergam
        </Typography>
        <Typography variant="subtitle1" component="h4" color="textSecondary">
          Connect your telegram account by inserting your ID here. So we can notify you
          for specific event that you have been set
        </Typography>
      </Grid>
      <Grid item lg={8} md={8} xs={12}>
        <Card>
          <CardContent className={classes.accountFieldContainer}>
            <Grid direction="column" container spacing={3}>
              <Grid item>
                <TextField
                  value={userTelegramID}
                  type={visibilityTelegramID ? "text" : "password"}
                  onChange={event => setUserTelegramID(event.target.value)}
                  variant="outlined"
                  id="telegram-ID"
                  label="Telegram ID"
                  InputProps={{
                    endAdornment: <InputAdornment position="end">
                      <IconButton onClick={() => setVisibilityTelegramID(state => state = !state)}>
                        {visibilityTelegramID ? <Visibility /> : <VisibilityOff />}
                      </IconButton>
                    </InputAdornment>
                  }} />
              </Grid>
            </Grid>
          </CardContent>
          <CardActions className={classes.accountCardAction}>
            <Button onClick={handleUpdateTelegram} variant="contained" color="primary">
              Save
            </Button>
          </CardActions>
        </Card>
      </Grid>
    </React.Fragment>
  )
}