import ApiService, { generateCancelToken } from '@services/apiService';
import Cookie from 'js-cookie';
import React, { useEffect, useState } from 'react';

export const COOKIE_LOGIN_STATE = 'codersbox_state';
export const COOKIE_LOGIN_TOKEN = 'codersbox_session';


const initialState = {
  isLoggedIn: false,
  userName: '',
  userEmail: '',
  userId: 0,
  authToken: '',
  userTelegramID: 0,
  handleLogout: async () => { },
  handleLogin: async (credentials) => { },
  handleAutoLogin: async () => { },
}


export const AuthContext = React.createContext(initialState);

export default function AuthContextProvider(props) {

  const apiCancelToken = generateCancelToken();
  const api = new ApiService(apiCancelToken);
  const [isLoggedIn, setIsLoggedIn] = useState(() => Boolean(Cookie.get(COOKIE_LOGIN_STATE)));
  const [userName, setUserName] = useState('');
  const [userEmail, setUserEmail] = useState('');
  const [userId, setUserId] = useState(0);
  const [userTelegramID, setTelegramID] = useState(0);
  // const [authToken, setAuthToken] = useState(undefined);


  /**
   * ---------------------------------
   *  Login Handler
   * ---------------------------------
   *  Set User to Login. If success redirect to dashbox Page.
   * 
   * @param {*} credentials 
   */
  const handleLogin = async (credentials) => {
    try {
      await api.getUserCookie();
      const response = await api.findUser(credentials);
      const userData = response.data.data;

      setIsLoggedIn(true);
      setUserName(userData.userName);
      setUserEmail(userData.email);
      setUserId(userData.userId);
      setTelegramID(userData.telegramID);


      Cookie.set(COOKIE_LOGIN_STATE, true, { expires: 1, sameSite: 'Lax' });

    } catch (error) {
      setIsLoggedIn(false);
      console.log(error);
    }
  }

  /**
   * ---------------------------------
   *  Logout Handler
   * ---------------------------------
   *  Set User to Logout. If success redirect to Login page.
   * 
   * @param {*} credentials 
   */
  const handleLogout = async () => {
    try {
      const response = await api.userLogout();
      const userData = response.data.data;

      Cookie.remove(COOKIE_LOGIN_STATE, { expires: 1, sameSite: 'Lax' });
      Cookie.remove(COOKIE_LOGIN_TOKEN, { expires: 1, sameSite: 'Lax' });

      setIsLoggedIn(false);
      setUserName('');
      setUserEmail('');
      setUserId(0);
      // setAuthToken('');
      console.log(userData)
    } catch (error) {
      console.log(error);
    }
  }

  /**
   * ---------------------------------
   *  Auto Login Handler
   * ---------------------------------
   *  Set User to Login If Cookie was set. If success redirect to dashbox Page.
   * 
   */
  const handleAutoLogin = async () => {
    try {
      const response = await api.userAutoLoginCheck(Cookie.get(COOKIE_LOGIN_TOKEN));
      const userData = response.data.data;

      setIsLoggedIn(true);
      setUserName(userData.userName);
      setUserEmail(userData.email);
      setUserId(userData.userId);
      setTelegramID(userData.telegramID);

      Cookie.set(COOKIE_LOGIN_STATE, true, { expires: 1, sameSite: 'Lax' });
    } catch (error) {
      setIsLoggedIn(false);
      Cookie.remove(COOKIE_LOGIN_STATE, { expires: 1, sameSite: 'Lax' });
      Cookie.remove(COOKIE_LOGIN_TOKEN, { expires: 1, sameSite: 'Lax' });
      console.log(error);
    }
  }


  useEffect(() => {

    if (Boolean(Cookie.get(COOKIE_LOGIN_STATE)) === true) {
      handleAutoLogin();
    }

    if (Boolean(Cookie.get(COOKIE_LOGIN_STATE)) === false && Cookie.get(COOKIE_LOGIN_STATE) !== undefined) {
      handleLogout();
    }

    return () => apiCancelToken.cancel();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <AuthContext.Provider
      value={{
        isLoggedIn,
        userName,
        userId,
        userEmail,
        userTelegramID,
        handleLogin,
        handleLogout,
        handleAutoLogin,
      }}
    >
      {props.children}
    </AuthContext.Provider>
  )
}
