import { createSlice, PayloadAction } from "@reduxjs/toolkit";

export interface WidgetDataState {
  widgetDataContainer: Object;
}

const initialState: WidgetDataState = {
  widgetDataContainer: {},
}

const WidgetDataSlice = createSlice({
  name: 'WidgetDataSlice',
  initialState: initialState,
  reducers: {
    setWidgetDataContainer: (state, action: PayloadAction<Object>) => {
      state.widgetDataContainer = {...state.widgetDataContainer, ...action.payload};
    },
  },
});

export const {setWidgetDataContainer} = WidgetDataSlice.actions
export default WidgetDataSlice.reducer;