import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import CircularProgress from '@material-ui/core/CircularProgress';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import DeleteBoxIcon from '@material-ui/icons/Delete';
import ShareBoxIcon from '@material-ui/icons/Share';
import ViewBoxIcon from '@material-ui/icons/Visibility';
import ApiService, { generateCancelToken } from '@services/apiService';
import { useAppDispatch, useTypedReduxSelector } from '@store/Store';
import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
/** ICONS */
import codersLogo from '../../Image/cardbox-whiteblue.png';
import { deleteUserBox } from './Redux/DashboardSlice';


const useStyles = makeStyles({
  root: {

  },
  media: {
    height: 140,
  },
});

const apiCancelToken = generateCancelToken();
const api = new ApiService(apiCancelToken);

export default function CardBox({ name, description, boxToken }) {
  const classes = useStyles();
  const dispatch = useAppDispatch();
  const dashboardState = useTypedReduxSelector(state => state.dashboardPage);
  const [openDialog, setOpenDialog] = useState(false);
  const [isDeleting, setIsDeleting] = useState(false);
  const handleDeleteBox = () => {
    setIsDeleting(true);

    api.deleteBox(boxToken)
      .then(() => {
        dispatch(deleteUserBox(boxToken));
      })
      .then(() => {
        setIsDeleting(false);
        setOpenDialog(false);
      })
      .catch(error => {
        console.log(error);
        setIsDeleting(false);
      })
  }

  const handleCloseDialog = () => {
    setOpenDialog(false);
  }

  const handleOpenDialog = () => {
    setOpenDialog(true);
  }

  useEffect(() => {
    return () => {
      if(dashboardState.boxes.length === 0){
        apiCancelToken.cancel('request canceled');
      }
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <React.Fragment>
      <Card className={classes.root}>
        <CardActionArea>
          <CardMedia
            className={classes.media}
            image={codersLogo}
            title={`Codersbox project: ${name}`}
          />
          <CardContent>
            <Typography gutterBottom variant="h5" component="h2">
              {name}
            </Typography>
            <Typography variant="body2" color="textSecondary" component="p">
              {description}
            </Typography>
          </CardContent>
        </CardActionArea>
        <CardActions>
          <Button startIcon={<ShareBoxIcon />} component={Link} to="/test" size="small" color="primary">
            Share
          </Button>
          <Button startIcon={<ViewBoxIcon />} component={Link} to={`/box/${boxToken}`} size="small" color="primary">
            View
          </Button>
          <Button startIcon={<DeleteBoxIcon />} onClick={handleOpenDialog} size="small" color="primary">
            Delete
          </Button>
        </CardActions>
      </Card>
      <Dialog
        open={openDialog}
        onClose={handleCloseDialog}
        aria-labelledby="alert-delete-box"
        aria-describedby="alert-confirmation-for-delete-box"
      >
        <DialogTitle id="alert-delete-box">{"You want to delete this Box?"}</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-confirmation-for-delete-box">
            Its ok if you want to delete, you can add other box nicely in the next time!
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button disabled={isDeleting} onClick={handleCloseDialog} color="primary">
            Wait a minute
          </Button>
          <Button disabled={isDeleting} onClick={() => handleDeleteBox()} color="primary" autoFocus>
            {isDeleting ? <CircularProgress color="primary" /> : 'Delete'}
          </Button>
        </DialogActions>
      </Dialog>
    </React.Fragment>
  );
}