import { useAppState } from '@globalContext/AppContextTracked';
import { Button, ButtonGroup, Tooltip, Typography } from '@material-ui/core';
import CircularProgress from '@material-ui/core/CircularProgress';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import RestoreIcon from '@material-ui/icons/Restore';
import Skeleton from '@material-ui/lab/Skeleton';
import ApiService, { generateCancelToken } from '@services/apiService';
import { setFetchingState, setFetchingStatus } from '@store/AppSlice';
import { useAppDispatch, useTypedReduxSelector } from '@store/Store';
import React, { useEffect, useState } from 'react';


const useStyles = makeStyles({
  table: {
    minWidth: 650,
  },
});


export default function BasicTable() {

  const apiCancelToken = generateCancelToken();
  const api = new ApiService(apiCancelToken);
  const dispatch = useAppDispatch();
  const appState = useTypedReduxSelector(state => state.app);
  const classes = useStyles();
  const [boxTrashState, setBoxTrashState] = useState([]);


  const fetchBoxTrash = async () => {
    try {
      dispatch(setFetchingStatus(true));
      const response = await api.getBoxTrash();
      const boxData = response.data.data;
      setBoxTrashState(boxData);
      dispatch(setFetchingState({
        message: 'Box Trash loaded successfully!',
        severity: 'success',
        isOpen: true,
      }));
    } catch (error) {
      console.error('Trash Page', error);
      dispatch(setFetchingState({
        message: 'Box Trash loaded failed!',
        severity: 'error',
        isOpen: true,
      }));
    }
  }

  const handleRestore = async (boxToken) => {
    try {
      dispatch(setFetchingStatus(true));
      await api.getBoxTrashRestore(boxToken);
      setBoxTrashState(state => state.filter(box => box.boxToken !== boxToken));
      dispatch(setFetchingState({
        message: 'Box was restored successfully!',
        severity: 'success',
        isOpen: true,
      }));
    } catch (error) {
      console.error('Trash Page', error);
      dispatch(setFetchingState({
        message: 'Box Trash restore failed!',
        severity: 'error',
        isOpen: true,
      }));
    }
  }

  const handleRemove = async (boxToken, boxDeviceToken) => {
    try {
      dispatch(setFetchingStatus(true));
      await api.getBoxTrashDelete(boxToken, boxDeviceToken);
      setBoxTrashState(state => state.filter(box => box.boxToken !== boxToken));
      dispatch(setFetchingState({
        message: 'Box was deleted permanently',
        severity: 'warning',
        isOpen: true,
      }));
    } catch (error) {
      console.error('Trash Page', error);
      dispatch(setFetchingState({
        message: 'Box Trash delete permanently failed!',
        severity: 'error',
        isOpen: true,
      }));
    }
  }

  useEffect(() => {
    fetchBoxTrash();
    return () => {
      if (boxTrashState.length !== 0) {
        apiCancelToken.cancel()
      }
    };

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <Grid item lg={12} md={12} xs={12}>
      <TableContainer component={Paper}>
        <Table className={classes.table} aria-label="Box Trash">
          <TableHead>
            <TableRow>
              <TableCell>Box Name</TableCell>
              <TableCell align="center">Box Tag Name</TableCell>
              <TableCell align="center">Box Unique Token</TableCell>
              <TableCell align="center">Device Unique Token</TableCell>
              <TableCell align="center">Delete At</TableCell>
              <TableCell align="center">Actions</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {boxTrashState.length > 0 ?
              boxTrashState.map(box => (
                <TableRow hover key={box.boxToken}>
                  <TableCell component="th" scope="row">
                    {box.boxName}
                  </TableCell>
                  <TableCell align="center">{box.boxTagName}</TableCell>
                  <TableCell align="center">{box.boxToken}</TableCell>
                  <TableCell align="center">{box.boxDeviceToken}</TableCell>
                  <TableCell align="center">{box.deleteTime}</TableCell>
                  <TableCell align="center">
                    {appState.fetchingStatus ? (
                      <CircularProgress size={25} color="primary" />
                    ) : (
                      <ButtonGroup variant="contained">
                        <Tooltip title="Restore your box to active">
                          <Button onClick={() => handleRestore(box.boxToken)} color="primary"><RestoreIcon /></Button>
                        </Tooltip>
                        <Tooltip title="Delete Your Box Permanently">
                          <Button onClick={() => handleRemove(box.boxToken, box.boxDeviceToken)} color="secondary"><DeleteForeverIcon /></Button>
                        </Tooltip>
                      </ButtonGroup>
                    )}
                  </TableCell>
                </TableRow>
              )) : appState.fetchingStatus ? (
                <TableRow>
                  <TableCell component="th" scope="row">
                    <Skeleton animation="wave" />
                  </TableCell>
                  <TableCell align="center"><Skeleton animation="wave" /></TableCell>
                  <TableCell align="center"><Skeleton animation="wave" /></TableCell>
                  <TableCell align="center"><Skeleton animation="wave" /></TableCell>
                  <TableCell align="center"><Skeleton animation="wave" /></TableCell>
                </TableRow>
              ) : (
                <TableRow>
                  <TableCell colSpan={5} align="center">
                    <Typography variant="subtitle1" component="h4" color="textSecondary">
                      No Box Trashed
                    </Typography>
                  </TableCell>
                </TableRow>
              )}
          </TableBody>
        </Table>
      </TableContainer>
    </Grid>
  );
}