import { Grid, InputBase } from "@material-ui/core";
import Slider from "@material-ui/core/Slider";
import { withStyles } from "@material-ui/core/styles";
import useWidgetControlHandler from "@pages/BoxPage/hooks/WidgetControlHandler";
import useWidgetDataHandler from "@pages/BoxPage/hooks/WidgetDataHandler";
import clsx from "clsx";
import React from "react";
import useStyles from "./Styles";

const PrettoSlider = withStyles({
  root: {
    color: "#52af77",
    height: 8,
  },
  thumb: {
    height: 24,
    width: 24,
    backgroundColor: "#fff",
    border: "2px solid currentColor",
    marginTop: -8,
    marginLeft: -12,
    "&:focus, &:hover, &$active": {
      boxShadow: "inherit",
    },
  },
  active: {},
  valueLabel: {
    left: "calc(-50% + 4px)",
  },
  track: {
    height: 8,
    borderRadius: 4,
  },
  rail: {
    height: 8,
    borderRadius: 4,
  },
})(Slider);

export default function SliderWidgetComponent({
  widgetId,
  widgetProperties,
  widgetData,
  widgetOptions,
}) {
  const [data] = useWidgetDataHandler(
    "Slider",
    widgetData,
    widgetOptions,
    widgetId,
    0
  );
  const handler = useWidgetControlHandler(
    "Slider",
    widgetData,
    widgetOptions,
    widgetId
  );
  const classes = useStyles();

  return (
    <Grid container>
      <Grid item xs={12}>
        <PrettoSlider
          max={Number(widgetOptions?.maxValue)}
          min={Number(widgetOptions?.minValue)}
          orientation={widgetOptions.orientation || "horizontal"}
          valueLabelDisplay="auto"
          aria-label="codersbox-slider"
          value={handler.newData}
          onChange={(event, newValue) => handler.handleControl(newValue)}
        />
      </Grid>
      <Grid container item xs={12}>
        <Grid item xs={6}>
          Device Value:
          <InputBase
            className={classes.input}
            inputProps={{ "aria-label": "0" }}
            value={data}
          />
        </Grid>
        <Grid item xs={6}>
          Setpoint:
          <InputBase
            className={clsx(classes.input, classes.inputSetpoint)}
            inputProps={{ "aria-label": "0" }}
            value={handler.newData}
          />
        </Grid>
      </Grid>
    </Grid>
  );
}
