import { setWidgetUIuseOwnDataField } from '@pages/BoxPage/Redux/WidgetUISlice';
import { useAppDispatch } from '@store/Store';
import React, { useEffect } from 'react';
import WidgetFormOptions from '../WidgetFormOptions';

export default function ChartRadialWidgetForm() {
  const dispatch = useAppDispatch();
  
  useEffect(() => {
    dispatch(setWidgetUIuseOwnDataField(false));

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <React.Fragment>
      <WidgetFormOptions.FormLabel />
      <WidgetFormOptions.ColorPicker />
      <WidgetFormOptions.MetricUnit />
      <WidgetFormOptions.MinMaxValue captionText="Radial Widget use this values to be remapped from 0% to 100%. For example, if you have 10 as your minimum value, and 60 as your maximum value, so 0% is equal to 10, and 100% is equal to 60." />
    </React.Fragment>
  );
}