import React, { useContext, useEffect, useState } from "react"; //ini urang tambahin titik koma
import Button from "@material-ui/core/Button";
import CssBaseline from "@material-ui/core/CssBaseline";
import TextField from "@material-ui/core/TextField";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import Link from "@material-ui/core/Link";
import Grid from "@material-ui/core/Grid";
import Box from "@material-ui/core/Box";
import CircularProgress from "@material-ui/core/CircularProgress";
import Typography from "@material-ui/core/Typography";
import Container from "@material-ui/core/Container";
import useStyles from "./Styles.js";
import { useForm } from "react-hook-form";
import Logo from "../../Components/Logo.js";
import Snackbar from "@material-ui/core/Snackbar";
import MuiAlert from "@material-ui/lab/Alert";
import { useHistory } from "react-router";
import { AuthContext } from "@globalContext/AuthContext.js";

function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {"Copyright  "}
      <Link color="inherit" href="https://material-ui.com/">
        Coders box
      </Link>{" "}
      {new Date().getFullYear()}
      {"."}
    </Typography>
  );
}

export default function Login() {
  const classes = useStyles();
  const route = useHistory();
  const auth = useContext(AuthContext);
  const [loginFetchinStatus, setLoginFetchinStatus] = useState({
    isFetching: false,
    isAlert: false,
    message: "",
  });
  const { register, getValues, errors } = useForm({
    mode: "all",
    reValidateMode: "all",
    defaultValues: {
      email: "",
      password: "",
      remember: false,
    },
  });

  /** Handler */
  const handleErrorSnackBar = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    setLoginFetchinStatus({ isFetching: false, isAlert: false, message: "" });
  };

  /**
   *
   *  Login Handler
   */

  const onLoginSuccess = loginStatus => {
    console.log('Redirecting...')
    setLoginFetchinStatus({ isFetching: false, isAlert: false, message: "" });
    route.push('/dashbox');
  };

  const onLoginFailed = (error) => {
    console.log(error);
    setLoginFetchinStatus({
      isFetching: false,
      isAlert: true,
      message: `Login error, code: ${error.response?.status}`,
    });
  };

  const onButtonLoginClick = () => {
    let dataUser = {
      email: getValues().email,
      password: getValues().password,
      remember: getValues().remember,
    };
    if (Object.keys(errors).length === 0) {
      setLoginFetchinStatus({ isFetching: true, isAlert: false, message: "" });
      auth.handleLogin(dataUser).then(() => onLoginSuccess()).catch(error => onLoginFailed(error));
    }
  };

  useEffect(() => {
    if (auth.isLoggedIn) {
      route.push('/dashbox');
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <Logo />
        <form method="post" className={classes.form}>
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            id="email"
            label="Email Address"
            name="email"
            autoComplete="email"
            autoFocus
            disabled={loginFetchinStatus.isFetching}
            inputRef={register({
              required: true,
              pattern: {
                value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                message: "You must provide a valid email address!",
              },
            })}
            error={errors.email}
            className={classes.margin}
          />
          {errors.email && (
            <span className={classes.error}>{errors.email.message}</span>
          )}

          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            name="password"
            label="Password"
            type="password"
            id="password"
            autoComplete="current-password"
            disabled={loginFetchinStatus.isFetching}
            inputRef={register({
              required: true,
              minLength: {
                value: 8,
                message: "Your password must be greater than 8 characters",
              },
            })}
            error={errors.password}
          />
          {errors.password && (
            <span className={classes.error}>{errors.password.message}</span>
          )}
          <FormControlLabel
            control={<Checkbox color="primary" />}
            label="Remember me"
            name="remember"
            inputRef={register}
            disabled={loginFetchinStatus.isFetching}
          />
          <Button
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
            onClick={() => onButtonLoginClick()}
            disabled={loginFetchinStatus.isFetching}
          >
            {loginFetchinStatus.isFetching ? (
              <CircularProgress color="primary" />
            ) : (
              "Login"
            )}
          </Button>
          <Grid container>
            <Grid item xs>
              <Link href="#" variant="body2">
                Forgot password?
              </Link>
            </Grid>
            <Grid item>
              <Link href="/signup" variant="body2">
                {"Don't have an account? Sign Up"}
              </Link>
            </Grid>
          </Grid>
        </form>
      </div>
      <Box mt={8}>
        <Copyright />
      </Box>
      <Snackbar
        open={loginFetchinStatus.isAlert}
        autoHideDuration={6000}
        onClose={handleErrorSnackBar}
      >
        <Alert onClose={handleErrorSnackBar} severity="error">
          {loginFetchinStatus.message}
        </Alert>
      </Snackbar>
    </Container>
  );
}

require("react-dom");
