import { MQTTService } from "./mqttService";

const hostname = '1d0a4037d8f645b9ab69895b4d041632.s1.eu.hivemq.cloud';
const port = 8884;

class CodersboxService extends MQTTService {

  constructor() {
    super(hostname, port);
    this.boxToken = '';
    this.setAuth('akun.unduhan21@gmail.com', 'b4ndun9em4ng@Hivemq');
    this.setSSL(true);
    ;
  }

  setBoxToken(boxToken) {
    this.boxToken = boxToken;
    const publish = `/codersbox/nodemcu/sub/${this.boxToken}`;
    const subscribe = `/codersbox/nodemcu/pub/${this.boxToken}`;
    this.setTopic(publish, subscribe);
  }

  getInstance()
  {
    return this.client;
  }
}

export default CodersboxService;