import useWidgetDataHandler from '@pages/BoxPage/hooks/WidgetDataHandler';
import format from 'date-fns/format';
import getTime from 'date-fns/getTime';
import subHours from 'date-fns/subHours';
import React, { useEffect, useState } from 'react';
import ReactApexChart from 'react-apexcharts';


export default function ChartLineComponent({ widgetId, widgetProperties, widgetData, widgetOptions }) {
  const [data,] = useWidgetDataHandler('ChartLine', widgetData, widgetOptions, widgetId,[0,0]);

  const [chartOptions, setChartOptions] = useState({
    chart: {
      id: 'LineChart',
      type: 'line',
      animations: {
        enabled: true,
        easing: 'linear',
        dynamicAnimation: {
          speed: 300
        }
      },
      zoom: {
        enabled: true,
        autoScaleYaxis: true
      }
    },
    stroke: {
      curve: 'straight'
    },
    grid: {
      row: {
        colors: ['#f3f3f3', 'transparent'], // takes an array which will be repeated on columns
        opacity: 0.5
      },
    },
    xaxis: {
      type: 'datetime',
      range: Date.now() - getTime(subHours(Date.now(), 1)),
      tickPlacement: 'on',
      tickAmount: 6,
      labels: {
        format: 'MM yyyy',
        formatter: function (value, timestamp, opts) {
          return format(new Date(timestamp), "dd LLL | kk:mm:ss");
        },
      }
    },
    tooltip: {
      x: {
        format: 'dd MMM yyyy | hh:mm:ss'
      }
    },
    noData: {
      text: 'No Data Available',
      align: 'center',
      verticalAlign: 'middle',
      offsetX: 0,
      offsetY: 0,
    },

  });

  const [dataChart, setDataChart] = useState([]);
  const [series, setSeries] = useState([]);


  useEffect(() => {
    if (widgetData.source === 'datafield' && series.length === 0) {
      if (Array.isArray(widgetData.value) && widgetOptions.isMultiSeries) {
        widgetOptions.label.map((label, index) => {
          let newData = {
            name: label,
            data: [],
          };

          setDataChart(state => [...state, newData]);
        });
      }
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [widgetOptions]);


  useEffect(() => {
    let newData = [];
    if (Array.isArray(widgetData.value) && widgetOptions.isMultiSeries) {

      widgetOptions.label.map((label, index) => {
        if (dataChart[index]?.name === label) {
          newData[index] = {
            name: label,
            data: [...dataChart[index]?.data, { x: new Date().getTime(), y: data[widgetData.value[index]] }],
          }
          setDataChart(newData);
        }
      });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [data]);

  useEffect(() => {
    setSeries(dataChart);

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [dataChart]);

  useEffect(() => {
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);


  return (
    <React.Fragment>
      <ReactApexChart style={{ marginTop: -40 }} options={chartOptions} series={series} type="line" height={350} width={'100%'} />
    </React.Fragment>
  )
}