import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { WidgetLayout } from "./WidgetSlice";

export interface WidgetUILayout {
  widgetsLayout: WidgetLayout[];
  useOwnDataField: boolean;
  widgetIndexLayout: number;
}

const initialState: WidgetUILayout = {
  widgetsLayout: [],
  useOwnDataField: false,
  widgetIndexLayout: 0,
}

const WidgetUISlice = createSlice({
  name: 'WidgetUISlice',
  initialState: initialState,
  reducers: {
    resetWidgetUILayout: (state) => {
      state.widgetsLayout = [];
    },
    setWidgetUILayout: (state, action: PayloadAction<WidgetLayout>) => {
      state.widgetsLayout = state.widgetsLayout.concat(action.payload);
    },
    setWidgetUIuseOwnDataField: (state, action: PayloadAction<boolean>) => {
      state.useOwnDataField = action.payload;
    },
    setWidgetIndexLayout: (state, action: PayloadAction<number>) => {
      state.widgetIndexLayout = action.payload;
    }
  }
});

export const {
  resetWidgetUILayout,
  setWidgetUILayout,
  setWidgetUIuseOwnDataField,
  setWidgetIndexLayout,
} = WidgetUISlice.actions;
export default WidgetUISlice.reducer;