import produce from 'immer';
import { useCallback, useState } from 'react';
import { createContainer } from 'react-tracked';

export let initialState = {

  isBoxEditMode: false,
  shouldBoxRefresh: false,
  mqtt: {},
  /**
   *  --------------
   *  State based on databox from API
   *  --------------
   */
  boxId: 0,
  boxToken: '',
  boxDeviceToken: '',
  boxName: '',
  boxTagName: '',
  boxDescription: '',
  boxActiveStatus: true,
  boxSettings: {
    dataField: [],
    layout: {},
    activeConnection: '',
    connection: {
      mqtt: {
        broker: '',
        subscribeTopic: '',
        publishTopic: '',
        authentication: ''
      },
      firebase: {
        databaseType: '',
        firebaseToken: '',
      },
      codersbox: {
        deviceToken: '',
      }
    }
  },
  boxWidgets: [],
  createdTime: '',
  updatedTime: '',

  /**
   *  --------------
   *  State for detect cols change
   *  --------------
   */
  boxCols: 0,

  /**
   *  --------------
   *  State for Box Connection Field
   *  --------------
   * 
   *  Used for determine how many fields that should be provided
   *  on Box Settings Page, Connection Tab
   */
  connectionsList: [],
  connectionFields: {},

  /**
   *  --------------
   *  State for Box Widget Creation
   *  --------------
   * 
   *  Used for determine how many fields that should be provided
   *  on Box Settings Page, Connection Tab
   */
  boxWidgetCreator:[],
};

const useAllBoxState = () => useState(initialState);

export const useSetBoxDraft = () => {
  const setBoxState = useBoxSetState();
  return useCallback(
    (draftUpdater) => {
      setBoxState(produce(draftUpdater));
    },
    [setBoxState],
  );
};

export const {
  Provider: BoxStateProvider,
  useTracked: useBoxState,
  useUpdate: useBoxSetState,
  useTrackedState: useBoxStateValue,
} = createContainer(useAllBoxState);
