import { useAppState } from '@globalContext/AppContextTracked';
import { Drawer, Grid, Typography } from '@material-ui/core';
import { useWidgetState } from '@pages/BoxPage/Context/widgetContextTracked';
import { setBoxUIEditWidget } from '@pages/BoxPage/Redux/BoxUISlice';
import { useAppDispatch, useTypedReduxSelector } from '@store/Store';
import React, { useState } from 'react';
import useStyles from '../Styles';
import EditWidgetForm from './EditWidgetForm';


export default function EditWidgetDrawer() {
  const dispatch = useAppDispatch();
  const boxUIState = useTypedReduxSelector(state => state.boxPageUI);
  const widgetState = useTypedReduxSelector(state => state.widget);
  const classess = useStyles();

  const onDrawerClose = () => {
    dispatch(setBoxUIEditWidget(false));
  }

  return (
    <React.Fragment>
      <Drawer anchor={'right'} open={boxUIState.isEditWidget} onClose={onDrawerClose}>
        <Grid className={classess.widgetFormEditMargin} container direction='column'>
          <Grid item xs={12}>
            <Grid item xs={12} md={8} lg={9}>
              <Grid container spacing={3} justify="space-between" alignItems="center" direction="row">
                <Grid item xs={8} md={8} lg={10}>
                  <Typography variant="h4" component="h2">
                    Edit Widget
                  </Typography>
                  <Typography variant="subtitle1" component="h4" color="textSecondary">
                    {widgetState.properties?.title || 'waiting...'}
                  </Typography>
                </Grid>
                <Grid item xs={4} md={4} lg={2}>
                </Grid>
              </Grid>
              <br />
            </Grid>
            <Grid item xs={12}>
              <EditWidgetForm />
            </Grid>
          </Grid>
        </Grid>
      </Drawer>
    </React.Fragment>
  );
}