import { createSlice, PayloadAction } from "@reduxjs/toolkit";


export interface BoxSettingsInitialState {
  connectionsList: string[];
  connectionFields: string[];
}

export const initialState: BoxSettingsInitialState = {
  connectionsList: [],
  connectionFields: [],
}

const BoxSettingsSlice = createSlice({
  name: 'BoxSettingsSlice',
  initialState: initialState,
  reducers: {
    setBoxSettingsConnectionFields: (state, action: PayloadAction<string[]>) => {
      state.connectionFields = action.payload;
    },
    setBoxSettingsConnectionList: (state, action: PayloadAction<string[]>) => {
      state.connectionsList = action.payload;
    },
  }
});


export const {
  setBoxSettingsConnectionFields,
  setBoxSettingsConnectionList
} = BoxSettingsSlice.actions;
export default BoxSettingsSlice.reducer;

