import { Button, Checkbox, FormControl, FormControlLabel, Grid, IconButton, InputLabel, MenuItem, Select, TextField } from '@material-ui/core';
import AddIcons from '@material-ui/icons/AddBox';
import DeleteIcons from '@material-ui/icons/Clear';
import { deleteWidgetArrayDataValue, deleteWidgetArrayOption, resetWidgetDataValueArray, resetWidgetOptionArray, setWidgetArrayDataValue, setWidgetArrayOption, setWidgetDataSource, setWidgetOption } from '@pages/BoxPage/Redux/WidgetSlice';
import { setWidgetUIuseOwnDataField } from '@pages/BoxPage/Redux/WidgetUISlice';
import { useAppDispatch, useTypedReduxSelector } from '@store/Store';
import React, { useEffect, useState } from 'react';


export default function ChartLineWidgetForm() {
  const dispatch = useAppDispatch();
  const boxState = useTypedReduxSelector(state => state.box);
  const widgetState = useTypedReduxSelector(state => state.widget);
  const [isMultiSeries, setMultiSeries] = useState(false);
  const [chartSeries, setChartSeries] = useState([]);
  const [chartSeriesCount, setChartSeriesCount] = useState(0);


  const handleWidgetFieldName = (seriesIndex, seriesField) => {

    setChartSeries(state => {
      let copy = state.slice();
      copy[seriesIndex].dataField = seriesField;
      state = copy;
      return state;
    });

    dispatch(setWidgetArrayDataValue({
      index: seriesIndex,
      value: seriesField
    }));
  }

  const handleMultiSeries = multiSeriesCheck => {
    setMultiSeries(prev => prev = !prev);
    dispatch(setWidgetUIuseOwnDataField(!multiSeriesCheck));
    dispatch(setWidgetOption({isMultiSeries: !multiSeriesCheck}));
  }

  const handleSeriesLabel = (seriesIndex, seriesLabel) => {

    setChartSeries(state => {
      let copy = state.slice();
      copy[seriesIndex].labelName = seriesLabel;
      state = copy;
      return state;
    });
    dispatch(setWidgetArrayOption({
      optionName: 'label',
      index: seriesIndex,
      value: seriesLabel,
    }));
  }

  const handleAddNewSeries = () => {
    let newData = {
      idCount: chartSeriesCount,
      dataField: 0,
      fieldName: `Field Series - ${chartSeriesCount}`,
      labelName: `Series ${chartSeriesCount} label`,
    };
    setChartSeriesCount(count => count + 1);
    setChartSeries(state => ([...state, newData]));
  }

  const handleDeleteSeries = (idCount, dataFieldName, labelName) => {
    setChartSeries(state => state.filter(series => series.idCount !== idCount));
    dispatch(deleteWidgetArrayDataValue(dataFieldName));
    dispatch(deleteWidgetArrayOption({
      optionName: 'label',
      value: labelName
    }));
  }


  //set to initial if series data = 0
  useEffect(() => {
    if (chartSeries.length === 0) {
      setChartSeriesCount(0);
      dispatch(setWidgetDataSource("datafield"));
      dispatch(resetWidgetDataValueArray());
      dispatch(resetWidgetOptionArray({ optionName: 'label' }));
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [chartSeries]);


  //Set for the first loading
  useEffect(() => {
    dispatch(setWidgetDataSource("datafield"));
    dispatch(resetWidgetDataValueArray());
    dispatch(resetWidgetOptionArray({ optionName: 'label' }));
    dispatch(setWidgetOption({
      isMultiSeries: false
    }));

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <React.Fragment>
      <Grid item xs={12} md={12} lg={12}>
        <FormControlLabel
          control={
            <Checkbox
              checked={isMultiSeries}
              onChange={() => handleMultiSeries(isMultiSeries)}
              name="checkedB"
              color="primary"
            />
          }
          label="Multi Series"
        />
      </Grid>
      <Grid style={{ display: isMultiSeries ? null : 'none' }} item xs={12} md={12} lg={12}>
        <Button
          variant="contained"
          style={{ display: isMultiSeries ? null : 'none' }}
          color="primary"
          startIcon={<AddIcons fontSize="large" />}
          onClick={handleAddNewSeries} aria-label="Add new series"
        >
          Add New Data Series
        </Button>
      </Grid>
      {
        isMultiSeries ?
          chartSeries.map((value, index) => (
            <React.Fragment key={index}>
              <Grid container spacing={2} direction="row" style={{ marginTop: 10 }}>
                <Grid item xs={5} md={5} lg={5}>
                  <FormControl variant="outlined" style={{ width: '100%' }}>
                    <InputLabel id="field-value-type">{value.fieldName}</InputLabel>
                    <Select
                      labelId="field-value-type"
                      id={`${widgetState.data.value[index]}-outlined`}
                      value={chartSeries[index].dataField || 0}
                      onChange={e => handleWidgetFieldName(index, e.target.value)}
                      label={value.fieldName}
                    >
                      <MenuItem value={0}>
                        <em>None</em>
                      </MenuItem>
                      {
                        boxState.boxSettings.dataField?.length !== 0 ?
                          boxState.boxSettings.dataField.map((field, i) => (
                            <MenuItem key={i} value={field}>{field}</MenuItem>
                          ))
                          : <MenuItem value={0}><em>None</em></MenuItem>
                      }
                    </Select>
                  </FormControl>
                </Grid>
                <Grid item xs={5} md={5} lg={5}>
                  <TextField value={widgetState.options?.label[index]} onChange={e => handleSeriesLabel(index, e.target.value)} variant="outlined" id={`series-${value.idCount}-label`} label={value.labelName} />
                </Grid>
                <Grid item xs={2} md={2} lg={2}>
                  <IconButton onClick={() => handleDeleteSeries(value.idCount, widgetState.data.value[index], widgetState.options.label[index])}>
                    <DeleteIcons />
                  </IconButton>
                </Grid>
              </Grid>
            </React.Fragment>
          ))
          :
          null
      }
    </React.Fragment>
  )
}