import { useState } from 'react';
import { createContainer } from 'react-tracked';

const initialState = {
  isDrawerOpen: true,
  isAddNewBox: false,
  isAddNewWidget: false,
  isEditWidget: false,

  /**
   *  Fetching Resource State
   * 
   */
  fetchingStatus: false,
  fetchingMessage: {
    message: '',
    severity: 'info',
    isOpen: false,
  },

  //Dashboard
  shouldRender: false,
  boxes: [],
};

const useAllAppState = () => useState(initialState);

/**
 * App State for managing All UI Interaction in Apps
 * 
 */
export const {
  Provider: AppStateProvider,
  useTracked: useAppState,
  useUpdate: useAppSetState,
  useTrackedState: useAppStateValue,
} = createContainer(useAllAppState);
