import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { string } from "prop-types";

export type boxcreatorfield = 'boxToken' | 'boxName' | 'boxTagName' | 'boxDescription';
export interface DashboardBoxUser {
  boxName: string;
  boxTagName: string;
  boxToken: string;
}

export interface DashboardBoxCreator {
  boxToken?: string;
  boxName: string;
  boxTagName: string;
  boxDescription: string;
  boxUserId?: number;
}

export interface DashboardInitialState {
  isAddNewBox: boolean;
  boxes: DashboardBoxUser[];
  newBoxCreator: DashboardBoxCreator;
}

const initialState: DashboardInitialState = {
  isAddNewBox: false,
  boxes: [],
  newBoxCreator: {
    boxToken: '',
    boxName: '',
    boxTagName: '',
    boxDescription: '',
    boxUserId: 0,
  },
};

const DashboardSlice = createSlice({
  name: "DashboardSlice",
  initialState: initialState,
  reducers: {
    setAddNewBox: (state, action: PayloadAction<boolean>) => {
      state.isAddNewBox = action.payload;
    },
    setUserBoxes: (state, action: PayloadAction<DashboardBoxUser[]>) => {
      state.boxes = action.payload;
    },
    setNewBoxCreator: (state, action: PayloadAction<{field: boxcreatorfield, value: string}>) => {
      state.newBoxCreator[action.payload.field] = action.payload.value;
    },
    deleteUserBox: (state, action: PayloadAction<string>) => {
      state.boxes = state.boxes.filter(box => box.boxToken !== action.payload);
    },
  },
});

export const { setAddNewBox, setUserBoxes, setNewBoxCreator, deleteUserBox } = DashboardSlice.actions;
export default DashboardSlice.reducer;
