import { Grid, Typography } from '@material-ui/core';
import { Skeleton } from '@material-ui/lab';
import ApiService, { generateCancelToken } from '@services/apiService';
import { setBoxShouldRefresh, setFetchingMessage, setFetchingStatus } from '@store/AppSlice';
import { useAppDispatch, useTypedReduxSelector } from '@store/Store';
import React, { useEffect, useState } from 'react';
import { matchPath } from 'react-router';
import { setBoxState, initialState } from '../Redux/BoxSlice';
import { setBoxUIShouldRefresh } from '../Redux/BoxUISlice';


export default function BoxContainer(props) {
  const dispatch = useAppDispatch();
  const boxState = useTypedReduxSelector(state => state.box);
  const boxUIState = useTypedReduxSelector(state => state.boxPageUI);
  const apiCancelToken = generateCancelToken();
  const api = new ApiService(apiCancelToken);
  const [isBoxFound, setBoxFound] = useState(true);
  const [boxFetchingStatus, setFetchingStatus] = useState(true);

  const getUrlParams = matchPath(window.location.pathname, {
    path: '/box/:uniqueBoxToken',
    exact: false,
    strict: false,
  });

  /**
   * Fetching new box data by using its token
   * 
   * @param {string} uniqueToken 
   * @returns Promise|void
   */
  const fetchBox = async (uniqueToken) => {
    if (uniqueToken === null || uniqueToken === undefined) {
      return Promise.reject();
    }
    // dispatch(setFetchingStatus(true));s
    setFetchingStatus(true);
    try {
      const getBox = await api.getBox(uniqueToken);
      const boxData = getBox.data.data;
      dispatch(
        setBoxState({
          boxId: boxData.boxId,
          boxToken: boxData.boxToken,
          boxDeviceToken: boxData.boxDeviceToken,
          boxName: boxData.boxName,
          boxTagName: boxData.boxTagName,
          boxDescription: boxData.boxDescription,
          boxActiveStatus: boxData.boxActiveStatus,
          boxSettings: boxData.boxSettings,
          boxWidgets: boxData.boxWidgets,
        })
      );
      setBoxFound(true);
      setFetchingStatus(false);

    } catch (error) {
      console.log(error);
      setBoxFound(false);
      setFetchingStatus(false);
    }
  }



  /**
   *  --------------
   *  Get box data based on shouldRefresh
   *  --------------
   */
  useEffect(() => {
    if ((boxUIState.shouldBoxRefresh === true) && (boxUIState.isBoxEditMode === false)) {
      dispatch(setFetchingMessage({
        message: 'Refreshing your dashbox..',
        severity: 'info',
        isOpen: true,
      }));
      fetchBox(boxState.boxToken);
      dispatch(
        setBoxUIShouldRefresh(false)
      );
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [boxUIState.shouldBoxRefresh]);

  /**
   *  --------------
   *  On First Loading Reset
   *  when there are some reasons
   *  --------------
   */
  useEffect(() => {
    if (getUrlParams.params.uniqueBoxToken !== undefined) {
      console.log('box fetching..');
      fetchBox(getUrlParams.params.uniqueBoxToken);
    }

    return () => {
        apiCancelToken.cancel();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <React.Fragment>
      {boxFetchingStatus ? <SkeletonLoading /> : isBoxFound ? props.children :
        <Grid alignContent="center" alignItems="center" justify="center" container spacing={3}>
          <Grid item xs={12} md={12} ld={12}>
            <Typography variant="h1" gutterBottom>
              404
            </Typography>
            <Typography variant="h2" gutterBottom>
              Box Not Found
            </Typography>
          </Grid>
        </Grid>
      }
    </React.Fragment>
  );
}

function SkeletonLoading() {
  return (
    <React.Fragment>
      <Skeleton height={80} width="40%" />
      <Skeleton height={20} width="60%" />
      <Grid container direction="row" spacing={3}>
        <Grid item xs={4}>
          <Skeleton height={200} width="100%" />
        </Grid>
        <Grid item xs={4}>
          <Skeleton height={200} width="100%" />
        </Grid>
        <Grid item xs={4}>
          <Skeleton height={200} width="100%" />
        </Grid>
      </Grid>
      <Grid container spacing={3}>
        <Grid item xs={4}>
          <Skeleton height={200} width="100%" />
        </Grid>
        <Grid item xs={4}>
          <Skeleton height={200} width="100%" />
        </Grid>
        <Grid item xs={4}>
          <Skeleton height={200} width="100%" />
        </Grid>
      </Grid>
    </React.Fragment>
  )
}
