import BoxSlice from "@pages/BoxPage/Redux/BoxSlice";
import BoxUISlice from "@pages/BoxPage/Redux/BoxUISlice";
import WidgetDataSlice from "@pages/BoxPage/Redux/WidgetDataSlice";
import WidgetSlice from "@pages/BoxPage/Redux/WidgetSlice";
import WidgetUISlice from "@pages/BoxPage/Redux/WidgetUISlice";
import DashboardSlice from "@pages/DashboardPage/Redux/DashboardSlice";
import BoxSettingsSlice from "@pages/SettingsPage/Redux/BoxSettingsSlice";
import { configureStore } from "@reduxjs/toolkit";
import {
  TypedUseSelectorHook,
  useDispatch as useReduxDispatch,
  useSelector as useReduxSelector
} from "react-redux";
import { createTrackedSelector } from "react-tracked";
import AppSlice from "./AppSlice";
import AuthSlice from "./AuthSlice";


const store = configureStore({
  reducer:{
    app: AppSlice,
    auth: AuthSlice,
    box: BoxSlice,
    widget: WidgetSlice,
    widgetUI: WidgetUISlice,
    widgetData: WidgetDataSlice,
    boxSettings: BoxSettingsSlice,
    dashboardPage: DashboardSlice,

    //PagesUI
    boxPageUI: BoxUISlice,
  }
});

export type RootState = ReturnType<typeof store.getState>;

export const useTypedReduxSelector: TypedUseSelectorHook<RootState> = useReduxSelector;
export const useTrackedSelector = createTrackedSelector(useTypedReduxSelector);

export const useAppDispatch = () => useReduxDispatch<typeof store.dispatch>();
export const useAppSelector = <T extends unknown>(selectFunc: (state: RootState) => T) => {
  const state = useTrackedSelector();
  return selectFunc(state);
};

export default store;
