import Paho from 'paho-mqtt';

export class MQTTService {


  constructor(hostName, port) {
    this.clientID = this.uuidv4();
    this.client = new Paho.Client(hostName, port, this.clientID);
    this.host = hostName;
    this.port = port;
    this.isSSL = false;
    this.message = {};
    this.topicPub = '';
    this.topicSubs = '';
    this.connectedState = false;
    this.username = '';
    this.password = '';
  }

  uuidv4() {
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
      var r = Math.random() * 16 | 0, v = c == 'x' ? r : (r & 0x3 | 0x8);
      return v.toString(16);
    });
  }
  

  handleConnectionLost(responseObject) {
    if (responseObject.errorCode !== 0) {
      console.log("onConnectionLost:" + responseObject.errorMessage);
    }
  }

  handleConnectionConnect() {
    try {
      const options = { qos: 0, onSuccess: function (context) { console.log("subscribed", context); } }
      this.client.subscribe(this.topicSubs, options);
      console.log('MQTT Connected. Client ID = ', this.clientID);
      this.connectedState = true;
    } catch (error) {
      console.log('connection mqtt state false')
      this.connectedState = false;
    }
  }

  getSubscribe() {
    const options = { qos: 0, onSuccess: function (context) { console.log("subscribed", context); } }
    this.client.subscribe(this.topicSubs, options);
  }

  close() {
    this.client.disconnect();
  }

  /**
   * 
   * @param {string} data 
   */
  sendData(data) {
    const message = new Paho.Message(data);
    message.destinationName = this.topicPub;
    message.qos = 1;
    this.client.send(message);
  }

  setSSL(isSSL) {
    this.isSSL = isSSL;
  }


  setTopic(publish, subscribe) {
    this.topicPub = publish;
    this.topicSubs = subscribe;
  }

  setAuth(username, password) {
    this.username = username;
    this.password = password;
  }

  handleMessageArived(message) {
    // this.message = JSON.parse(message.payloadString);
    console.log('MQTT ', message.payloadString);
  }

  getMessage() {
    return this.message;
  }

  connect() {
    this.client.connect({
      userName: this.username,
      password: this.password,
      useSSL: this.isSSL,
      onSuccess: this.handleConnectionConnect.bind(this)
    });
  }

  /**
   * 
   * @param {Function} callback 
   */
  handle(callback) {
    this.client.onConnectionLost = this.handleConnectionLost.bind(this);
    this.client.onMessageArrived = callback === undefined ? this.handleMessageArived.bind(this) : (message) => callback(message);
  }
}