import React from 'react';
import brandImageWhite from '../Image/codersbox-white.png';
import brandImageOriginal from '../Image/codersbox-original.png';
import brandImageGray from '../Image/codersbox-gray.png';
import originalBox from '../Image/originalicon.png';
import useStyles from './Styles';

export default function Logo({logoType, logoSize}) {
  const classes = useStyles();
  const sizeConfig = () => {
    switch(logoSize) {
      case 'small':
        return classes.logoImageSmall;
        
      case 'medium':
        return classes.logoImageMedium;
      
      case 'large':
        return classes.logoImageLarge;

      default:
        return classes.logoImage;
    }
  }


  return(
    <React.Fragment>
      {logoType === 'white' ? <img src={brandImageWhite} alt="Codersbox-Logo" className={sizeConfig()} />
        : logoType === 'gray' ? <img src={brandImageGray} alt="Codersbox-Logo" className={sizeConfig()} />
          : logoType === 'boxicon' ? <img src={originalBox} alt="Codersbox-Logo" className={sizeConfig()} />
            : <img src={brandImageOriginal} alt="Codersbox-Logo" className={sizeConfig()} /> }
    </React.Fragment>
  );
}