import { Grid, IconButton } from '@material-ui/core';
import useWidgetDataHandler from '@pages/BoxPage/hooks/WidgetDataHandler';
import clsx from 'clsx';
import React from 'react';
import useStyles from './Styles';

export default function LedWidgetComponent({ widgetId, widgetProperties, widgetData, widgetOptions }) {
  const classes = useStyles();;
  const [data,] = useWidgetDataHandler('Led', widgetData, widgetOptions, widgetId);

  return (
    <React.Fragment>
      <Grid spacing={2} container direction="row" justifyContent="center" alignItems="center">
        <Grid item justifyContent="center" alignContent="center" xs={6} md={6} lg={6}>
          <IconButton
            className={clsx(classes.ledRoot, {
              [classes.ledOnRed]: !data,
              [classes.ledOff]: data,
            })}>
          </IconButton>
        </Grid>
        <Grid item justifyContent="center" alignContent="center" xs={6} md={6} lg={6}>
          <IconButton
            className={clsx(classes.ledRoot, {
              [classes.ledOnGreen]: data,
              [classes.ledOff]: !data,
            })}>
          </IconButton>
        </Grid>
      </Grid>
    </React.Fragment>
  )
}