import CodersboxService from '@services/CodersboxService';
import { firebaseFireStore, firebaseRTDB } from '@services/firebaseService';
import { setFetchingMessage } from '@store/AppSlice';
import { useAppDispatch, useTypedReduxSelector } from '@store/Store';
import { useEffect } from 'react';
import { setWidgetDataContainer } from './Redux/WidgetDataSlice';

export const cdbservice = new CodersboxService();

export default function BoxConnectionManager() {
  const dispatch = useAppDispatch();
  const boxState = useTypedReduxSelector(state => state.box);

  const firebaseConnection = () => {
    if (Object.keys(boxState.boxSettings).length !== 0) {
      const firebaseConfig = JSON.parse(boxState.boxSettings.connection.firebase.firebaseToken);
      const dbtype = boxState.boxSettings.connection.firebase.databaseType;
      if (dbtype === 'rtdb') {
        try {
          const codersboxFirebase = firebaseRTDB(firebaseConfig, boxState.boxToken);
          codersboxFirebase.on('value', snapshot => {
            let widgetData = { ...snapshot.val().data, ...snapshot.val().control };
            dispatch(setWidgetDataContainer(widgetData));
          });
          dispatch(
            setFetchingMessage({
              message: `Connected to firebase RTDB! `,
              severity: 'success',
              isOpen: true,
            })
          );
        }
        catch (error) {
          console.log(error);
          dispatch(
            setFetchingMessage({
              message: `Failed to firebase RTDB! `,
              severity: 'error',
              isOpen: true,
            })
          );
        }
      } else {
        try {
          const codersboxFirebase = firebaseFireStore(firebaseConfig);
          codersboxFirebase.collection(boxState.boxToken).orderBy('create_at', 'desc').limit(1)
            .onSnapshot(doc => {
              // doc.forEach(item => setWidgetState(state => ({ ...state, widgetData: item.data() })));
              dispatch(setFetchingMessage({
                message: `Connected to firebase RTDB! `,
                severity: 'success',
                isOpen: true,
              }));
            });
          dispatch(
            setFetchingMessage({
              message: `Connected to firebase Firestore! `,
              severity: 'success',
              isOpen: true,
            })
          );
        }
        catch (error) {
          console.log(error);
          dispatch(
            setFetchingMessage({
              message: `Failed to firebase Firestore! `,
              severity: 'error',
              isOpen: true,
            })
          );
        }
      }
    }
  }

  const codersboxConnection = () => {
    try {
      cdbservice.setBoxToken(boxState.boxToken);
      cdbservice.connect();

      dispatch(
        setFetchingMessage({
          message: `Connected with Codersbox! `,
          severity: 'success',
          isOpen: true,
        })
      );

      cdbservice.handle(message => {
        // console.log('topic subs', JSON.parse(message.payloadString), cdbservice.getInstance().isConnected());
        dispatch(setWidgetDataContainer(JSON.parse(message.payloadString)));
      });
    }
    catch (error) {
      console.log(error);
      dispatch(
        setFetchingMessage({
          message: `Failed connect to Codersbox!`,
          severity: 'success',
          isOpen: true,
        })
      );
    }
  }

  useEffect(() => {
    switch (boxState.boxSettings.activeConnection) {
      default:
        return;

      case 'codersbox':
        codersboxConnection();
        break;

      case 'firebase':
        firebaseConnection();
        break;

      case 'mqtt':
        // let defaultPort = 8081;
        // let defaultBroker = '';

        // const getParse = boxState.boxSettings.connection.mqtt.broker.split(":");
        // if (getParse[1] !== undefined) {
        //   defaultPort = parseInt(getParse[1]);
        //   defaultBroker = getParse[0];
        // }
        // else {
        //   defaultBroker = boxState.boxSettings.connection.mqtt.broker;
        // }

        // const mqtt = new MQTTService(
        //   defaultBroker,
        //   defaultPort,
        //   boxState.boxToken,
        //   boxState.boxSettings.connection.mqtt.publishTopic,
        //   boxState.boxSettings.connection.mqtt.subscribeTopic,
        //   false,
        // );

        // if (mqtt.connectedState) {
        //   console.log('MQTT Connected');
        //   setAppState(state => ({
        //     ...state,
        //     fetchingMessage: {
        //       message: `Connected to ${defaultBroker} on ${defaultPort} `,
        //       severity: 'success',
        //       isOpen: true,
        //     }
        //   }))
        // }

        // mqtt.handle(message => setWidgetState(
        //   state => ({
        //     ...state,
        //     WidgetDataContainer: JSON.parse(message.payloadString)
        //   })));
        break;
    }

    return () => {
      if (cdbservice.connectedState) {
        cdbservice.close();
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [boxState.boxSettings.activeConnection]);

  return null;
}