import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import Cookie from 'js-cookie';


export const COOKIE_LOGIN_STATE = 'codersbox_state';
export const COOKIE_LOGIN_TOKEN = 'codersbox_session';

interface AuthState {
  isLoggedIn: false,
  userName: '',
  userEmail: string,
  userId: number,
  authToken: string,
  userTelegramID: number,
}

const initialState: AuthState = {
  isLoggedIn: false,
  userName: '',
  userEmail: '',
  userId: 0,
  authToken: '',
  userTelegramID: 0,
}

const AuthSlice = createSlice({
  name: 'AuthSlice',
  initialState: initialState,
  reducers: {
    handleLogout: (state, action: PayloadAction<AuthState>) => {
      state.isLoggedIn = false;
      state.userEmail = '';
      state.userId = 0;
      state.userName = '';
      state.userTelegramID = 0;
      state.authToken = '';
      Cookie.remove(COOKIE_LOGIN_STATE, { expires: 1, sameSite: 'Lax' });
      Cookie.remove(COOKIE_LOGIN_TOKEN, { expires: 1, sameSite: 'Lax' });
    },
    handleLogin: (state, action: PayloadAction<AuthState>) => {
      state.isLoggedIn = action.payload.isLoggedIn;
      state.userEmail = action.payload.userEmail;
      state.userId = action.payload.userId;
      state.userName = action.payload.userName;
      state.userTelegramID = action.payload.userTelegramID;
      state.authToken = action.payload.authToken;
      Cookie.set(COOKIE_LOGIN_STATE, 'true', { expires: 1, sameSite: 'Lax' });
    },
    handleAutoLogin: (state, action) => { },
  }
});

export const {handleLogout, handleLogin} = AuthSlice.actions;
export default AuthSlice.reducer;