import React from 'react';
import { Grid, Divider } from '@material-ui/core';
import SettingsTabs from './Components/Tabs';
import { Skeleton } from '@material-ui/lab';
import Loading from '../BoxPage/Components/loadingBar';
import { useAppStateValue } from '@globalContext/AppContextTracked';
import SettingBoxPageContainer from './Components/SettingBoxPageContainer';
import { useTypedReduxSelector } from '@store/Store';

const SettingsHeader = React.lazy(() => import('./Components/header'));

export default function BoxSettings(props) {
  const appState = useTypedReduxSelector(state => state.app);
  
  return (
    <SettingBoxPageContainer>
      <Grid container spacing={3} direction="column">
        <React.Suspense fallback={<Skeleton variant='rect' width={400} height={40} />}>
          <SettingsHeader />
        </React.Suspense>
        <Divider />
        {appState.fetchingStatus ? (<Loading width={400} height={500} />) : (<SettingsTabs />)}
      </Grid>
    </SettingBoxPageContainer>
  )
}