import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { Widget } from "./WidgetSlice";

export interface BoxInitialState {
  boxId: number;
  boxToken: string;
  boxDeviceToken: string;
  boxName: string;
  boxTagName: string;
  boxDescription: string;
  boxActiveStatus: boolean;
  boxSettings: BoxSettings;
  boxWidgets: Widget[];
  createdTime: string;
  updatedTime: string;
}

export interface BoxSettings {
  dataField: string[];
  layout: Object;
  activeConnection: string;
  connection: Object;
}

export const initialState: BoxInitialState = {
  boxId: 0,
  boxToken: '',
  boxDeviceToken: '',
  boxName: '',
  boxTagName: '',
  boxDescription: '',
  boxActiveStatus: true,
  boxSettings: {
    dataField: [],
    layout: {},
    activeConnection: '',
    connection: {
      mqtt: {
        broker: '',
        subscribeTopic: '',
        publishTopic: '',
        authentication: ''
      },
      firebase: {
        databaseType: '',
        firebaseToken: '',
      },
      codersbox: {
        deviceToken: '',
      }
    }
  },
  boxWidgets: [],
  createdTime: '',
  updatedTime: '',
};

const BoxSlice = createSlice({
  name: 'BoxSlice',
  initialState: initialState,
  reducers: {
    setBoxId: (state, action: PayloadAction<number>) => {
      state.boxId = action.payload;
    },
    setBoxState: (state, action: PayloadAction<BoxInitialState>) => {
      state.boxId = action.payload.boxId;
      state.boxToken = action.payload.boxToken;
      state.boxDeviceToken = action.payload.boxDeviceToken;
      state.boxName = action.payload.boxName;
      state.boxTagName = action.payload.boxTagName;
      state.boxDescription = action.payload.boxDescription;
      state.boxActiveStatus = action.payload.boxActiveStatus;
      state.boxSettings = action.payload.boxSettings;
      state.boxWidgets = action.payload.boxWidgets;
    },
    setBoxByFieldName: (state, action: PayloadAction<{field: string; value: string;}>) => {
      state[action.payload.field] = action.payload.value;
    },
    addBoxNewDataField: (state) => {
      state.boxSettings.dataField = [...state.boxSettings.dataField, ''];
    },
    setBoxNewDataFieldValue: (state, action:PayloadAction<{index: number; value: string}>) => {
      state.boxSettings.dataField[action.payload.index] = action.payload.value.replace(/ /g,"_");
    },
    deleteBoxDataField: (state, action: PayloadAction<string>) => {
      state.boxSettings.dataField = state.boxSettings.dataField.filter(field => field !== action.payload);
    },
    setBoxActiveConnection: (state, action: PayloadAction<string>) => {
      state.boxSettings.activeConnection = action.payload;
    },
    setBoxConnectionProperties: (state, action: PayloadAction<{selectedConnection: string; connectionPropsName: string; value: string;}>) => {
      state.boxSettings.connection[action.payload.selectedConnection][action.payload.connectionPropsName] = action.payload.value;
    },
    addBoxNewWidget: (state, action: PayloadAction<Widget>) => {
      state.boxWidgets = [...state.boxWidgets, action.payload];
    },
    deleteBoxWidgetByIndex: (state, action: PayloadAction<string>) => {
      const localDelete = JSON.parse(String(localStorage.getItem(`boxLayout-${state.boxToken}`))).filter(widget => widget.i !== action.payload);
      localStorage.setItem(`boxLayout-${state.boxToken}`, JSON.stringify(localDelete));
      const index = state.boxWidgets.findIndex(widget => widget.layout.i === action.payload);
      if (index >= 0) state.boxWidgets.splice(index, 1);
    },
    resetBoxSlice: (state) => {
      state = initialState;
    },
  },
});

export const {
  setBoxId,
  setBoxState,
  setBoxByFieldName,
  addBoxNewDataField,
  setBoxNewDataFieldValue,
  deleteBoxDataField,
  setBoxActiveConnection,
  setBoxConnectionProperties,
  addBoxNewWidget,
  deleteBoxWidgetByIndex,
  resetBoxSlice
} = BoxSlice.actions;
export default BoxSlice.reducer;
