import { useAppStateValue } from '@globalContext/AppContextTracked';
import { choosenWidget } from '@pages/BoxPage/Components/AddWidget/AddWidgetsCatalog';
import { useBoxState } from '@pages/BoxPage/Context/boxContextTracked';
import { useTypedReduxSelector } from '@store/Store';
import React, { useEffect, useRef, useState } from 'react';
import { Responsive, WidthProvider } from "react-grid-layout";
import WidgetCardContainer from './WidgetCardContainer';

const ResponsiveReactGridLayout = WidthProvider(Responsive);

export default function WidgetResponsiveGrid(props) {
  const appState = useTypedReduxSelector(state => state.app);
  const boxUIState = useTypedReduxSelector(state => state.boxPageUI);
  const timerOut = useRef();
  const boxState = useTypedReduxSelector(state => state.box);
  const [allLayoutsState, setAllLayouts] = useState();
  const [testState, setStateTest] = useState({
    currentBreakpoint: "lg",
    compactType: "vertical",
    mounted: false,
  });

  const onBreakpointChange = (breakpoint, cols) => {
    setStateTest(prev => ({
      ...prev,
      currentBreakpoint: breakpoint
    }));
  }

  const handleLayoutChange = (currentLayouts, allLayouts) => {
    localStorage.setItem(`boxLayout-${boxState.boxToken}`, JSON.stringify(currentLayouts));
    localStorage.setItem(`boxLayout-${boxState.boxToken}-allLayouts`, JSON.stringify(allLayouts));
    setAllLayouts(allLayouts);
    console.info('layout change', allLayouts);
  }

  useEffect(() => {
    timerOut.current = setTimeout(() => window.dispatchEvent(new Event('resize')), 180);
    return () => {
      clearTimeout(timerOut.current);
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [appState.isDrawerOpen, props.initialLayout]);

  useEffect(() => {
    setStateTest(prev => ({ ...prev, mounted: true }));
    return () => {
      clearTimeout(timerOut.current);
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <React.Fragment>
      <ResponsiveReactGridLayout
        {...props}
        className="layout"
        layouts={allLayoutsState}
        isDraggable={boxUIState.isBoxEditMode}
        isResizable={boxUIState.isBoxEditMode}
        breakpoints={{ lg: 1440, md: 1024, sm: 768, xs: 425, xxs: 320 }}
        cols={{ lg: 12, md: 10, sm: 4, xs: 2, xxs: 1 }}
        onBreakpointChange={onBreakpointChange}
        onLayoutChange={handleLayoutChange}
        measureBeforeMount={false}
        useCSSTransforms={testState.mounted}
        compactType={testState.compactType}
        preventCollision={!testState.compactType}
      >
        {props.initialLayout.map((l, i) => (
          <div key={l.i} data-grid={l}>
            <WidgetCardContainer
              idWidget={boxState.boxWidgets[i]?.widgetId}
              indexWidget={l.i}
              widgetTitle={boxState.boxWidgets[i]?.properties.title}
              widgetSubtitle={boxState.boxWidgets[i]?.properties.subtitle}
              widgetOptions={boxState.boxWidgets[i]?.options}>

              {/* Showing Widget JSX Component */}
              {choosenWidget(
                boxState.boxWidgets[i]?.widgetId,
                boxState.boxWidgets[i]?.self?.variant,
                boxState.boxWidgets[i]?.properties,
                boxState.boxWidgets[i]?.data,
                boxState.boxWidgets[i]?.options,
              )}
            </WidgetCardContainer>
          </div>
        ))}

      </ResponsiveReactGridLayout>
    </React.Fragment>
  )
}

WidgetResponsiveGrid.defaultProps = {
  className: "layout",
  rowHeight: 30,
  onLayoutChange: function () { },
  cols: { lg: 12, md: 6, sm: 4, xs: 3, xxs: 1 },
};