import {
  Button,
  Dialog, DialogActions, DialogContent, DialogContentText,
  DialogTitle, useMediaQuery,
  useTheme
} from '@material-ui/core';
import { setBoxUIAddNewWidget } from '@pages/BoxPage/Redux/BoxUISlice';
import { setAddNewWidget } from '@store/AppSlice';
import { useAppDispatch, useTypedReduxSelector } from '@store/Store';
import React from 'react';
import WidgetsCatalog from './AddWidgetsCatalog';

export default function AddWidget(props) {
  const theme = useTheme();
  const dispatch = useAppDispatch();
  const appState = useTypedReduxSelector(state => state.app);
  const boxUIState = useTypedReduxSelector(state => state.boxPageUI);
  const fullscreen = useMediaQuery(theme.breakpoints.down("sm"));

  const dialogOnClose = () => {
    dispatch(
      setBoxUIAddNewWidget(false)
    );
  }

  return (
    <React.Fragment>
      <Dialog onClose={dialogOnClose} open={boxUIState.isAddNewWidget} fullScreen={fullscreen}>
        <DialogTitle id="responsive-dialog-title">{"Add new: Widget"}</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Add new widget here. Just click and choose your data field. We'll make it for you.
          </DialogContentText>
          {/** Widgets catalogue goes here */}
          <WidgetsCatalog />
        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={dialogOnClose} color="primary">
            Close
          </Button>
        </DialogActions>
      </Dialog>
    </React.Fragment>
  )
}