import { useAppState } from '@globalContext/AppContextTracked';
import { Button, IconButton, Menu, MenuItem } from '@material-ui/core';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Fade from '@material-ui/core/Fade';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import { useDeleteWidget } from '@pages/BoxPage/hooks/useWidgetBoxUtility';
import { deleteBoxWidgetByIndex } from '@pages/BoxPage/Redux/BoxSlice';
import { setBoxUIEditWidget } from '@pages/BoxPage/Redux/BoxUISlice';
import { setWidgetData, setWidgetId, setWidgetOption, setWidgetProperties } from '@pages/BoxPage/Redux/WidgetSlice';
import ApiService, { generateCancelToken } from '@services/apiService';
import { setFetchingState, setFetchingStatus } from '@store/AppSlice';
import { useAppDispatch, useTypedReduxSelector } from '@store/Store';
import React, { useCallback, useState } from 'react';
import { useBoxState } from '../../Context/boxContextTracked';
import { useWidgetSetState } from '../../Context/widgetContextTracked';

const apiCancelToken = generateCancelToken();
const api = new ApiService(apiCancelToken);

export default function EditWidget({ idWidget, indexLayoutWidget }) {
  const dispatch = useAppDispatch();
  const boxUIState = useTypedReduxSelector(state => state.boxPageUI);
  const boxState = useTypedReduxSelector(state => state.box);
  const [anchorEl, setAnchorEl] = useState(null);
  const [openDialog, setOpenDialog] = useState(false);
  const open = Boolean(anchorEl);
  const deleteWidgetFromBox = useDeleteWidget(boxState.boxToken);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleCloseDialog = () => {
    setOpenDialog(false);
  }

  const handleOpenDialog = () => {
    setOpenDialog(true);
  }

  const handleDeleteWidget = async () => {

    try {
      dispatch(deleteBoxWidgetByIndex(indexLayoutWidget))
      dispatch(setFetchingStatus(true));

      await api.deleteWidgetOnBox(idWidget, boxState.boxToken);

      dispatch(setFetchingState({
        message: 'Widget succesfully deleted!',
        severity: 'warning',
        isOpen: true,
        fetchingStatus: false,
      }));
      setOpenDialog(false);
    } catch (error) {
      console.error('Edit Widget', error);
      dispatch(setFetchingState({
        message: 'Something wrong while delete your widget!',
        severity: 'error',
        isOpen: true,
        fetchingStatus: false,
      }));
      return;
    }
  }

  const handleEditWidget = useCallback(async () => {
    dispatch(setFetchingStatus(true));

    try {
      const response = await api.getWidgetOnBox(idWidget, boxState.boxToken);
      const widgetAPIData = response.data;

      dispatch(setWidgetId(idWidget));
      dispatch(setWidgetProperties(widgetAPIData.properties));
      dispatch(setWidgetData(widgetAPIData.data));
      dispatch(setWidgetOption(widgetAPIData.options));
      dispatch(setFetchingState({
        message: 'Widget Edit loaded!',
        severity: 'success',
        isOpen: true,
        fetchingStatus: false,
      }));
    } catch (error) {
      console.error('Edit Widget', error);
      dispatch(setFetchingState({
        message: 'Something wrong while take your widget!',
        severity: 'error',
        isOpen: true,
        fetchingStatus: false,
      }));
      return;
    }
  }, [idWidget]);

  return (
    <React.Fragment>
      <IconButton style={{ display: boxUIState.isBoxEditMode ? null : 'none' }} onClick={handleClick} aria-label="settings">
        <MoreVertIcon />
      </IconButton>
      <Menu
        id="fade-menu"
        anchorEl={anchorEl}
        keepMounted
        open={open}
        onClose={handleClose}
        TransitionComponent={Fade}
      >
        <MenuItem onClick={() => {
          dispatch(setBoxUIEditWidget(true));
          handleEditWidget();
        }}>Edit</MenuItem>
        <MenuItem onClick={handleOpenDialog}>Delete</MenuItem>
      </Menu>
      <Dialog
        open={openDialog}
        onClose={handleCloseDialog}
        aria-labelledby="alert-delete-widget"
        aria-describedby="alert-confirmation-for-delete-widget"
      >
        <DialogTitle id="alert-delete-widget">{"You want to delete this widget?"}</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-confirmation-for-delete-widget">
            Its ok if you want to delete, you can add other widget nicely in the next time!
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseDialog} color="primary">
            Wait a minute
          </Button>
          <Button onClick={handleDeleteWidget} color="primary" autoFocus>
            Delete
          </Button>
        </DialogActions>
      </Dialog>
    </React.Fragment>
  );
}