import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles(theme => ({
  /** Widget */
  fieldFlex : {
    flex: 1,
    '& .MuiTextField-root': {
      // margin: theme.spacing(1),
      width: '100%',
    },
    '& .MuiSelect-root': {
      // margin: theme.spacing(1),
      width: '100%',
    },
  },
  widgetRoot: {
    height: 'inherit',
  },
  widgetComponentSpacing: {
    paddingTop: theme.spacing(3),
  },
  widgetFormEditMargin: {
    maxWidth: 300,
    margin: theme.spacing(3),
  },
  widgetFormEditAppBar: {
    top: 'auto',
    bottom: 0,
  },

  widgetFormEditToolbar: {
    width: '100%',
    overflow:'hidden',
  },

  widgetCatalogCard: {
    flexGrow: 1,
    cursor: 'pointer',
  },

  widgetCatalogCardChoosed: {
    flexGrow: 1,
    margin: theme.spacing(2),
    padding: theme.spacing(1),
    minWidth: 240,
    backgroundColor: '#fafafa',
  },

  /** Dragging */
  dragOn:{
    opacity: 0,
  },
  dragOff: {
    opacity: 1,
  },

  /** Animation */
  choosedWidget: {
    minHeight: 250,
    transition: theme.transitions.create('height', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  unChoosedWidget: {
    display: 'none',
    transition: theme.transitions.create('height', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    minHeight: 120,
    [theme.breakpoints.up('sm')]: {
      height: theme.spacing(9) + 1,
    },
  },

  /** Chart */
  chartRoot: {
    flex: 1,
    minHeight: 200,
    minWidth: 200,
    flexGrow: 1,
  },

  /** Form Options */
  colorPicker:{
    position: 'absolute',
    zIndex: 100,
    left: 5,
    top: 200
  }
}));

export default useStyles;