import { Grid, Typography } from '@material-ui/core';
import ApiService, { generateCancelToken } from '@services/apiService';
import { setAppReRender, setFetchingMessage, setFetchingState, setFetchingStatus, setUserBox } from '@store/AppSlice';
import { useAppDispatch, useTypedReduxSelector } from '@store/Store';
import React, { useEffect } from 'react';
import CardBox from './CardBox';
import CardBoxLoading from './Components/CardBoxLoading';
import QuickAddNewBox from './Components/QuickAddDialog';
import { setUserBoxes } from './Redux/DashboardSlice';



export default function Dashboard() {
  const apiCancelToken = generateCancelToken();
  const api = new ApiService(apiCancelToken);
  const appState = useTypedReduxSelector(state => state.app);
  const dashboardState = useTypedReduxSelector(state => state.dashboardPage);
  const dispatch = useAppDispatch();

  const handleGetUserBox = async () => {
    dispatch(setFetchingStatus(true));
    try {
      const response = await api.getBoxUser();
      dispatch(setUserBoxes(response.data.data.boxes));
      dispatch(setFetchingState({
        message: 'All box has been loaded!',
        severity: 'success',
        isOpen: true,
        fetchingStatus: false,
      }));
    } catch (error) {
      console.error('Dashboard page ', error);
      dispatch(setFetchingState({
        message: 'Box failed to load!',
        severity: 'error',
        isOpen: true,
        fetchingStatus: false,
      }));
    }
  }

  useEffect(() => {
    if (appState.shouldRender) {
      dispatch(setAppReRender(false));
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [appState.shouldRender]);

  useEffect(() => {
    if (dashboardState.boxes.length === 0) {
      // if (auth.userName !== undefined && auth.isLoggedIn) {
      handleGetUserBox();
      // }
    }

    return () => {
      if (dashboardState.boxes.length !== 0) {
        apiCancelToken.cancel();
      }
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  return (
    <React.Fragment>
      <Typography variant="h5" color="textPrimary" gutterBottom>Your box</Typography>

      <Grid container spacing={2}>
        {dashboardState.boxes.length !== 0 ? dashboardState.boxes.map((data, index) => (
          <Grid key={index} item xs={12} md={6} lg={3}>
            <CardBox name={data.boxName} description={data.boxTagName} boxToken={data.boxToken} />
          </Grid>
        )) : null}
        <Grid style={{ display: appState.fetchingStatus ? 'block' : 'none' }} item xs={12} md={4} lg={3}>
          <CardBoxLoading />
        </Grid>
        <QuickAddNewBox />
      </Grid>
    </React.Fragment>
  );
}