import { Divider, Grid, IconButton, InputBase, Paper } from '@material-ui/core';
import AddCircleIcon from '@material-ui/icons/AddCircle';
import RemoveCircleIcon from '@material-ui/icons/RemoveCircle';
import useWidgetControlHandler from '@pages/BoxPage/hooks/WidgetControlHandler';
import useWidgetDataHandler from '@pages/BoxPage/hooks/WidgetDataHandler';
import clsx from 'clsx';
import React, { useState } from 'react';
import useStyles from './Styles';

export default function StepperWidgetComponent({ widgetId, widgetProperties, widgetData, widgetOptions }) {
  const classes = useStyles();;
  const [data] = useWidgetDataHandler("Stepper",
    widgetData,
    widgetOptions,
    widgetId,
    0);
  const handler = useWidgetControlHandler('Stepper', widgetData, widgetOptions, widgetId);
  const [isLimit, setIsLimit] = useState([false, false]);

  const limitControl = (dataControl) => {
    if (dataControl < widgetOptions.minValue) {
      setIsLimit([true, false]);
      return;
    }

    if (dataControl > widgetOptions.maxValue) {
      setIsLimit([false, true]);
      return;
    }

    setIsLimit([false, false]);
    handler.handleControl(dataControl);
  }

  return (
    <React.Fragment>
      <Grid spacing={1} container direction="row" justifyContent="center" alignItems="center">
        <Grid item>
          Device Value: {data} of {widgetOptions.maxValue}
        </Grid>
        <Grid item justifyContent="center" alignContent="center" >
          <Paper component="form" className={classes.root}>
            <IconButton
              disabled={isLimit[0]}
              onClick={() => limitControl(handler.newData - 1)}
              className={classes.iconButton}>
              <RemoveCircleIcon className={clsx(classes.buttonIcon, {
                [classes.buttonIconMin]: !isLimit[0],
                [classes.buttonIconDisabled]: isLimit[0],
              })} />
            </IconButton>
            <Divider className={classes.divider} orientation="vertical" />
            <InputBase
              className={classes.input}
              inputProps={{ 'aria-label': '0' }}
              value={handler.newData}
            />
            <Divider className={classes.divider} orientation="vertical" />
            <IconButton
              disabled={isLimit[1]}
              onClick={() => limitControl(handler.newData + 1)}
              className={clsx(classes.iconButton, classes.buttonIconAdd)}>
              <AddCircleIcon className={clsx(classes.buttonIcon, {
                [classes.buttonIconAdd]: !isLimit[1],
                [classes.buttonIconDisabled]: isLimit[1],
              })} />
            </IconButton>
          </Paper>
        </Grid>
      </Grid>
    </React.Fragment>
  )
}