import ApiService, { generateCancelToken } from '@services/apiService';
import { useAppDispatch, useTypedReduxSelector } from '@store/Store';
import React, { useEffect } from 'react';
import { matchPath } from 'react-router';
import { setBoxSettingsConnectionFields, setBoxSettingsConnectionList } from '../Redux/BoxSettingsSlice';



export default function SettingBoxPageContainer(props) {
  const apiCancelToken = generateCancelToken();
  const api = new ApiService(apiCancelToken);
  const dispatch = useAppDispatch();
  const boxState = useTypedReduxSelector(state => state.box);

  const loadAvailableConnectionField = async () => {
    try {
      const connectionList = await api.getConnectionList();
      const dataList = connectionList.data.data;
      dispatch(setBoxSettingsConnectionFields(dataList.connectionFields));
      dispatch(setBoxSettingsConnectionList(dataList.connectionsList));
    } catch (error) {
      console.error('Box Settings Container', error);
    }
  }

  useEffect(() => {
    loadAvailableConnectionField();

    return () => {
      if(boxState.boxId !== 0) {
        apiCancelToken.cancel();
      }
    };

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  return (
    <React.Fragment>
      {props.children}
    </React.Fragment>
  )
}