import { Divider, Grid,} from '@material-ui/core';
import React from 'react';
import BasicTable from './Components/BoxApiTable';
import HeaderPage from './Components/HeaderPage';
import useStyles from './Styles';

export default function BoxAPIPage() {
  const classes = useStyles();

  return (
    <React.Fragment>
      <HeaderPage />
      <Divider />
      <Grid className={classes.accountContainer} container spacing={4}>
        <BasicTable />
      </Grid>
    </React.Fragment>
  )
}