import { useSetBoxDraft } from "@pages/BoxPage/Context/boxContextTracked";
import { useCallback } from "react";

/**
 * useAddWidget
 * 
 * Hook for adding widget to box
 * 
 * @returns {React.RefCallback} useCallback
 */
export function useAddWidget() {
  const setDraft = useSetBoxDraft();
  return useCallback((widgetCreator) => {
    setDraft(draft => {
      draft.boxWidgets.push(widgetCreator);
    });
  }, [setDraft]);
}


/**
 * useAddWidget
 * 
 * Hook for delete widget to box
 * 
 * @param {String} boxToken
 * @returns {React.RefCallback} useCallback
 */
export function useDeleteWidget(boxToken) {
  const setDraft = useSetBoxDraft();
  return useCallback((indexLayoutWidget) => {
    setDraft(draft => {
      const localDelete = JSON.parse(localStorage.getItem(`boxLayout-${boxToken}`)).filter(widget => widget.i !== indexLayoutWidget);
      localStorage.setItem(`boxLayout-${boxToken}`, JSON.stringify(localDelete));
      const index = draft.boxWidgets.findIndex(widget => widget.layout.i === indexLayoutWidget);
      if (index >= 0) draft.boxWidgets.splice(index, 1);
    });
  }, [setDraft]);
}