import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert from '@material-ui/lab/Alert';
import { setFetchingState } from '@store/AppSlice';
import { useAppDispatch, useTypedReduxSelector } from '@store/Store';
import React from 'react';


function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

/**
 *
 * Giving some alert and snackbar in every event in apps
 * especially when user did some data fetch
 * 
 * @returns JSX
 */
export default function AlertBar() {
  const appState = useTypedReduxSelector(state => state.app);
  const dispatch = useAppDispatch();

  const handleSnackBar = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }

    dispatch(
      setFetchingState({
        fetchingStatus: false,
        message: '',
        severity: 'info',
        isOpen: false,
      })
    );
  }

  return (
    <React.Fragment>
      <Snackbar open={appState.fetchingMessage.isOpen} autoHideDuration={6000} onClose={handleSnackBar}>
        <Alert onClose={handleSnackBar} severity={appState.fetchingMessage.severity}>
          {appState.fetchingMessage.message}
        </Alert>
      </Snackbar>
      <Snackbar open={appState.fetchingMessage.isOpen} autoHideDuration={6000} onClose={handleSnackBar}>
        <Alert onClose={handleSnackBar} severity={appState.fetchingMessage.severity}>
          {appState.fetchingMessage.message}
        </Alert>
      </Snackbar>
    </React.Fragment>
  )
}

