import { createSlice, PayloadAction } from "@reduxjs/toolkit";

type Severity = 'error' | 'success' | 'info' | 'warning';

export interface AppFetchingMessage {
  message: string;
  severity: Severity;
  isOpen: boolean;
}


export interface AppInitialState {
  shouldBoxRefresh: boolean;
  isDrawerOpen: boolean;

  //widget
  useOwnDataField: boolean;

  //fetching
  fetchingStatus: boolean;
  fetchingMessage: AppFetchingMessage;
  shouldRender: boolean;
}

const initialState: AppInitialState = {
  shouldBoxRefresh: false,
  isDrawerOpen: true,
  useOwnDataField: false,
  fetchingStatus: false,
  fetchingMessage: {
    message: "",
    severity: "info",
    isOpen: false,
  },
  shouldRender: false,
};

const AppSlice = createSlice({
  name: "AppSlice",
  initialState: initialState,
  reducers: {
    setDrawerState: (state, action: PayloadAction<boolean>) => {
      state.isDrawerOpen = action.payload;
    },
    setFetchingState: (state, action: PayloadAction<AppFetchingMessage & {fetchingStatus: boolean}>) => {
      state.fetchingMessage = action.payload;
      state.fetchingStatus = action.payload.fetchingStatus;
    },
    setFetchingMessage: (state, action: PayloadAction<AppFetchingMessage>) => {
      state.fetchingMessage = action.payload;
    },
    setFetchingStatus: (state, action: PayloadAction<boolean>) => {
      state.fetchingStatus = action.payload;
    },
    setAppReRender: (state, action: PayloadAction<boolean>) => {
      state.shouldRender = action.payload;
    },
    setBoxShouldRefresh: (state, action: PayloadAction<boolean>) => {
      state.shouldBoxRefresh = action.payload;
    },
    setUseOwnDataField: (state, action: PayloadAction<boolean>) => {
      state.useOwnDataField = action.payload;
    },
  },
});

export const { 
  setDrawerState, 
  setFetchingState, 
  setAppReRender,
  setFetchingMessage,
  setFetchingStatus,
  setBoxShouldRefresh,
} = AppSlice.actions;
export default AppSlice.reducer;
