import { Grid, IconButton } from '@material-ui/core';
import PowerSettingsNewIcon from '@material-ui/icons/PowerSettingsNew';
import useWidgetControlHandler from '@pages/BoxPage/hooks/WidgetControlHandler';
import useWidgetDataHandler from '@pages/BoxPage/hooks/WidgetDataHandler';
import clsx from 'clsx';
import React from 'react';
import useStyles from './Styles';

export default function ButtonWidgetComponent({ widgetId, widgetProperties, widgetData, widgetOptions }) {
  const classes = useStyles();;
  const [data, ] = useWidgetDataHandler('Button',widgetData, widgetOptions, widgetId);
  const handler = useWidgetControlHandler('Button',widgetData, widgetOptions, widgetId);

  return (
    <React.Fragment>
      <Grid container direction="column" justifyContent="center" alignItems="center">
        <Grid item>
          <IconButton
            onClick={() => handler.handleControl(!data)}
            className={clsx(classes.buttonRoot, {
              [classes.buttonOn]: data,
              [classes.buttonOff]: !data,
            })}>
            <PowerSettingsNewIcon className={classes.buttonIcon} />
          </IconButton>
        </Grid>
      </Grid>
    </React.Fragment>
  )
}