import { Button, Card, CardActions, CardContent, Grid, IconButton, InputAdornment, TextField, Typography } from '@material-ui/core';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import ApiService, { generateCancelToken } from '@services/apiService';
import { setFetchingState, setFetchingStatus } from '@store/AppSlice';
import { useAppDispatch } from '@store/Store';
import React, { useEffect, useState } from 'react';
import useStyles from '../Styles';

const apiCancelToken = generateCancelToken();
const api = new ApiService(apiCancelToken);

export default function FormPassword() {
  const classes = useStyles();
  const dispatch = useAppDispatch();
  const [activePassword, setActivePassword] = useState('');
  const [newPassword, setNewPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [visibilityPassword, setVisibilityPassword] = useState(false);

  const handleUpdatePassword = async () => {
    try {
      const dataUpdate = {
        activePassword: activePassword,
        password: newPassword,
        password_confirmation: confirmPassword,
      }
      dispatch(setFetchingStatus(true));
      await api.updateUserPassword(dataUpdate);
      dispatch(
        setFetchingState({
          message: 'Password changed successfully',
          isOpen: true,
          severity: 'success',
          fetchingStatus: false,
        })
      );
    } catch (error) {
      console.log(error);
      dispatch(
        setFetchingState({
          message: 'Password changed failed',
          isOpen: true,
          severity: 'error',
          fetchingStatus: false,
        })
      );
    }
  }

  useEffect(() => {
    return () => apiCancelToken.cancel();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <React.Fragment>
      <Grid item lg={4} md={4} xs={12}>
        <Typography variant="h5" >
          Password
        </Typography>
        <Typography variant="subtitle1" component="h4" color="textSecondary">
          Password management
        </Typography>
      </Grid>
      <Grid item lg={8} md={8} xs={12}>
        <Card>
          <CardContent className={classes.accountFieldContainer}>
            <Grid direction="column" container spacing={3}>
              <Grid item>
                <TextField
                  type={visibilityPassword ? "text" : "password"}
                  onChange={event => setActivePassword(event.target.value)}
                  variant="outlined"
                  id="active-password"
                  label="Active Password"
                  InputProps={{
                    endAdornment: <InputAdornment position="end">
                      <IconButton onClick={() => setVisibilityPassword(state => state = !state)}>
                        {visibilityPassword ? <Visibility /> : <VisibilityOff />}
                      </IconButton>
                    </InputAdornment>
                  }} />
              </Grid>
              <Grid item>
                <TextField
                  type={visibilityPassword ? "text" : "password"}
                  onChange={event => setNewPassword(event.target.value)}
                  variant="outlined"
                  id="new-password"
                  label="New Password"
                  InputProps={{
                    endAdornment: <InputAdornment position="end">
                      <IconButton onClick={() => setVisibilityPassword(state => state = !state)}>
                        {visibilityPassword ? <Visibility /> : <VisibilityOff />}
                      </IconButton>
                    </InputAdornment>
                  }} />
              </Grid>
              <Grid item>
                <TextField
                  type={visibilityPassword ? "text" : "password"}
                  onChange={event => setConfirmPassword(event.target.value)}
                  variant="outlined"
                  id="confirm-new-password"
                  label="Re-type new password"
                  InputProps={{
                    endAdornment: <InputAdornment position="end">
                      <IconButton onClick={() => setVisibilityPassword(state => state = !state)}>
                        {visibilityPassword ? <Visibility /> : <VisibilityOff />}
                      </IconButton>
                    </InputAdornment>
                  }} />
              </Grid>
            </Grid>
          </CardContent>
          <CardActions className={classes.accountCardAction}>
            <Button onClick={handleUpdatePassword} variant="contained" color="primary">
              Save
            </Button>
          </CardActions>
        </Card>
      </Grid>
    </React.Fragment>
  )
}