import { Checkbox, Divider, FormControl, FormControlLabel, FormGroup, Grid, InputLabel, MenuItem, Select, TextField, Typography } from '@material-ui/core';
import { setBoxActiveConnection, setBoxConnectionProperties } from '@pages/BoxPage/Redux/BoxSlice';
import { useAppDispatch, useTypedReduxSelector } from '@store/Store';
import React from 'react';
import useStyles from '../Styles';

export default function FormConnection() {
  const classes = useStyles();
  const dispatch = useAppDispatch();
  const boxState = useTypedReduxSelector(state => state.box);
  const boxSettingsState = useTypedReduxSelector(state => state.boxSettings);


  const handleActiveConnectionChange = e => {
    dispatch(setBoxActiveConnection(e.target.value));
  }

  const handleConnectionFieldChange = (value, fieldName, selectedConnection) => {
    dispatch(setBoxConnectionProperties({
      selectedConnection: selectedConnection,
      connectionPropsName: fieldName,
      value: value,
    }));
  }



  return (
    <React.Fragment>
      <Grid container spacing={4}>
        <Grid item xs={12} md={12} lg={3}>
          <Typography variant="h6" component="h6">
            Box Connection
          </Typography>
          <Typography variant="body1" component="h4" color="textSecondary">
            Set your connection here. Codersbox just gave you a freedom of connection. If you wish to use
            Firebase as your connection, just write here your token code. So let Codersbox arrange your data to
            your widget.
          </Typography>
          <FormControl className={classes.formConnection} variant="outlined" style={{ minWidth: 220, marginLeft: 0, marginTop: 20 }}>
            <InputLabel id="active-connection">Active Connection</InputLabel>
            <Select
              labelId="active-connection"
              id="active-connection"
              value={boxState.boxSettings.activeConnection}
              onChange={handleActiveConnectionChange}
              label="Active Connection"
            >
              <MenuItem value={null}>
                <em>None</em>
              </MenuItem>
              {boxSettingsState.connectionsList.map((connection, index) => (
                <MenuItem key={index} value={connection}>{connection}</MenuItem>
              ))}
            </Select>
          </FormControl>
        </Grid>

        {/* Divider */}
        <Divider orientation="vertical" flexItem />

        {/* Right Side */}
        <Grid item xs={12} md={12} lg={8}>
          <FormControl className={classes.formConnection}>
            {/* Generate Radio Field for each connection list */}
            {boxSettingsState.connectionsList.map((connectionName, index) => (
              <FormGroup key={index} className={classes.formConnectionField}>
                <Typography variant="h4" component="h2">
                  {connectionName.toUpperCase()}
                </Typography>
                <Divider variant="fullWidth" />
                {/* Generate Fields here */}
                {boxSettingsState.connectionFields[connectionName].map((field, fieldIndex) => (
                  <React.Fragment key={fieldIndex}>
                    {/* Generate Database type selection for Firebase */}
                    {field === 'databaseType' ? (
                      <FormControl variant="outlined" style={{ minWidth: 220, marginLeft: 0, marginTop: 20 }}>
                        <InputLabel disabled={false} id="firebase-database">Database type</InputLabel>
                        <Select
                          disabled={false}
                          labelId="firebase-database"
                          id="firebase-database"
                          value={boxState.boxSettings.connection[connectionName][field]}
                          onChange={event => handleConnectionFieldChange(event.target.value, field, connectionName)}
                          label="Database type"
                        >
                          <MenuItem value={null}>
                            <em>None</em>
                          </MenuItem>
                          <MenuItem value={'rtdb'}>Realtime Database</MenuItem>
                          <MenuItem value={'firestore'}>Firestore</MenuItem>
                        </Select>
                      </FormControl>
                    ) : field === 'TLS' ?
                      (
                        <FormControlLabel
                          control={
                            <Checkbox
                              checked={boxState.boxSettings.connection[connectionName][field]}
                              onChange={event => handleConnectionFieldChange(event.target.checked, field, connectionName)}
                              name="checkedB"
                              color="primary" />
                          }
                          label="Using TLS"
                        />
                      )
                      :
                      (
                        <TextField
                          //If its not firebase selection 
                          type={field === 'password'? 'password' : 'text'}
                          className={classes.formConnectionTextField}
                          disabled={false}
                          variant="outlined"
                          multiline={field === 'firebaseToken'}
                          rows={field === 'firebaseToken' ? 5 : 1}
                          id={field}
                          label={field}
                          // value={boxState.boxSettings.connection.mqtt.broker}
                          value={boxState.boxSettings.connection[connectionName][field]}
                          onChange={event => handleConnectionFieldChange(event.target.value, field, connectionName)}
                        />
                      )}
                  </React.Fragment>
                ))}
              </FormGroup>
            ))}
          </FormControl>
        </Grid>
      </Grid>
    </React.Fragment>
  )
}