import { Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, Divider, Grid, IconButton, InputAdornment, TextField, Typography } from '@material-ui/core';
import AddIcons from '@material-ui/icons/AddBox';
import DeleteIcons from '@material-ui/icons/Clear';
import { useBoxState } from '@pages/BoxPage/Context/boxContextTracked';
import { addBoxNewDataField, deleteBoxDataField, setBoxNewDataFieldValue } from '@pages/BoxPage/Redux/BoxSlice';
import { useAppDispatch, useTypedReduxSelector } from '@store/Store';
import React, { useEffect, useState } from 'react';
import useStyles from '../Styles';


/**
 * ---------
 * FormDataField
 * ---------
 * 
 * Provide Form Data for editing data fields that used on widget
 * 
 * @returns {JSX.Element} React Component 
 */
export default function FormDataField() {
  const classes = useStyles();
  const dispatch = useAppDispatch();
  const appState = useTypedReduxSelector(state => state.app);
  const boxState = useTypedReduxSelector(state => state.box);
  const [confirmDelete, setConfirmDelete] = useState(false);
  const [confirmDeleteFieldName, setConfirmDeleteField] = useState(undefined);
  

  const handleAddNewDataField = e => {
    if (Object.keys(boxState.boxSettings).length !== 0) {
      if (boxState.boxSettings.dataField.length <= 10) {
        dispatch(addBoxNewDataField());
      }
    }
  }

  const handleOnChange = (value, index) => {
    if (Object.keys(boxState.boxSettings).length !== 0) {
      dispatch(setBoxNewDataFieldValue({
        index: index,
        value:value,
      }));
    }
  }

  return (
    <Grid container spacing={4}>
      <ConfirmDeleteField onOpenChange={value =>setConfirmDelete(value)} fieldName={confirmDeleteFieldName} isOpen={confirmDelete} />
      <Grid item xs={12} md={12} lg={3}>
        <Typography variant="h6" component="h6">
          Data Field
        </Typography>
        <Typography variant="body1" component="h4" color="textSecondary">
          Tell us what data field's name that you wanna show on our widget. Like <i>'temperature', 'humidity'</i> or else. Or, add new one if it necessary. Dont use whitespace instead.
          Codersbox will trim all white space. <br /> e.g: <br /> <b><i>ambient temp = ambienttemp</i></b>
        </Typography>
        <br />
        <Divider orientation="horizontal" />
        <Button onClick={handleAddNewDataField}>
          <AddIcons />&nbsp;Add New Field
        </Button>
      </Grid>
      <Divider orientation="vertical" flexItem />
      <Grid item xs={12} md={12} lg={8}>
        <form className={classes.quickAddform}>
          <Grid container spacing={3}>
            {Object.keys(boxState.boxSettings).length !== 0 ?
              boxState.boxSettings.dataField.length !== 0 ?
                boxState.boxSettings.dataField.map((field, i) => (
                  <Grid key={i} item xs={12} md={12} lg={12}>
                    <TextField
                      disabled={appState.fetchingStatus}
                      variant="outlined" id={`data-field-${field}`}
                      label={`Data Field ${i+1}`}
                      value={field}
                      InputProps={{
                        endAdornment: <InputAdornment position="end">
                          <IconButton onClick={() => {
                            setConfirmDeleteField(field);
                            setConfirmDelete(true);
                            }}>
                            <DeleteIcons />
                          </IconButton>
                        </InputAdornment>
                      }}
                      onChange={event => handleOnChange(event.target.value, i)}/>
                  </Grid>
                )) : (
                  <Grid alignContent="center" item xs={12} md={12} lg={12}>
                    <Typography variant="h6" component="h6" color="textSecondary">
                      No Data Field Available here
                    </Typography>
                  </Grid>
                ) : (
                <Grid alignContent="center" item xs={12} md={12} lg={12}>
                  <Typography variant="h6" component="h6" color="textSecondary">
                    Failed
                    </Typography>
                </Grid>
              )}
          </Grid>
        </form>
      </Grid>
    </Grid>
  )
}

/**
 * ----------
 * ConfirmDeleteField
 * ----------
 * 
 * Provide Dialog to confirm that user want to delete their field
 * 
 * @param {{onOpenChange: function, isOpen: Boolean, fieldName: String}} props 
 * @returns {JSX.Element} Dialog Component
 */
function ConfirmDeleteField(props) {
  const [open, setOpen] = useState(false);
  const dispatch = useAppDispatch();
  const boxState = useTypedReduxSelector(state => state.box);

  const handleClose = () => {
    setOpen(false);
    props.onOpenChange(false);
  }

  const handleDeleteDataField = value => {
    if (Object.keys(boxState.boxSettings).length !== 0) {
      if (boxState.boxSettings.dataField.length >= 0) {
        dispatch(deleteBoxDataField(value));
      }
    }
  }

  useEffect(() => {
    if(props.isOpen){
      setOpen(true);
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [props.isOpen]);

  return (
    <Dialog
      open={open}
      onClose={handleClose}
      aria-labelledby="alert-dialog-title"
      aria-describedby="alert-dialog-description"
    >
      <DialogTitle id="alert-dialog-title">{`Delete for "${props.fieldName}" field?`}</DialogTitle>
      <DialogContent>
        <DialogContentText id="alert-dialog-description">
          By Deleting this field, all datas belongs to this {props.fieldName} field, won't be shown anymore on any widgets.
          </DialogContentText>
      </DialogContent>
      <DialogActions>
        <Button onClick={() => {
          handleDeleteDataField(props.fieldName);
          handleClose();
          }} color="primary">
          Yes, sure
        </Button>
        <Button onClick={handleClose} color="primary" autoFocus>
          Wait a minute 
        </Button>
      </DialogActions>
    </Dialog>
  )
}

ConfirmDeleteField.defaultProps = {
  onOpenChange: (value) => {},
  fieldName: '',
  isOpen: false
}