import firebase from 'firebase';

export function initFirebase(config = null) {
  if(!firebase.apps.length) {
    return firebase.initializeApp(config);
  }
  return firebase.app();
}

export function firebaseFireStore(firebaseConfig) {
  const codersboxFirebase = initFirebase(firebaseConfig);
  const db = codersboxFirebase.firestore();
  return db;
}

export function firebaseRTDB(firebaseConfig, refDocs) {
  const codersboxFirebase = initFirebase(firebaseConfig);
  const db = codersboxFirebase.database().ref(refDocs);
  return db;
}
