import { useTrackedSelector } from "@store/Store";
import { useEffect, useState } from "react";
import { WidgetData, WidgetOptions } from "../Redux/WidgetSlice";

export default function useWidgetDataHandler(
  widgetType: string,
  widgetData: WidgetData,
  widgetOptions: WidgetOptions,
  widgetId: number,
  defaultData?: number | string
) {
  const widgetDataState = useTrackedSelector().widgetData;
  const [data, setData] = useState<any>(defaultData || 0);

  //Change Every widgetDataState updated
  useEffect(() => {
    if (widgetData?.source === "manual") {
      setData(String(widgetData?.value));
    }

    if (widgetData?.source === "datafield") {
      if (typeof widgetData.value !== "object") {
        setData(
          isNaN(widgetDataState.widgetDataContainer[String(widgetData?.value)])
            ? 0
            : widgetDataState.widgetDataContainer[String(widgetData?.value)]
        );
      } else {
        const dataObject: Object = {};
        widgetData?.value.map((dataValue, index) => {
          dataObject[dataValue] = isNaN(
            widgetDataState.widgetDataContainer[String(dataValue)]
          )
            ? 0
            : widgetDataState.widgetDataContainer[String(dataValue)];
        });
        setData(dataObject);
      }
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [widgetDataState.widgetDataContainer]);

  useEffect(() => {
    localStorage.setItem(
      `lastData${widgetType}${widgetOptions?.label}-${widgetId}`,
      String(data)
    );
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [data]);

  return [data, setData];
}
