import { Button, Card, CardActions, CardContent, CardHeader, CircularProgress, FormControl, FormHelperText, Grid, InputLabel, MenuItem, Select, TextField, Typography } from '@material-ui/core';
import { addBoxNewWidget } from '@pages/BoxPage/Redux/BoxSlice';
import { setBoxUINewWidgetChoosed } from '@pages/BoxPage/Redux/BoxUISlice';
import { resetWidgetSlice, setWidgetDataSource, setWidgetDataValue, setWidgetId, setWidgetLayout, setWidgetSelfVariant, setWidgetSubtitle, setWidgetTitle } from '@pages/BoxPage/Redux/WidgetSlice';
import { setWidgetIndexLayout } from '@pages/BoxPage/Redux/WidgetUISlice';
import ApiService, { generateCancelToken } from '@services/apiService';
import { setFetchingState, setFetchingStatus } from '@store/AppSlice';
import { useAppDispatch, useTypedReduxSelector } from '@store/Store';
import clsx from 'clsx';
import React, { useEffect } from 'react';
import useStyles from '../Styles';

const apiCancelToken = generateCancelToken();
const api = new ApiService(apiCancelToken);

/**
 * 
 * @param {string} widgetName
 * @param {JSX} FormComponent
 * @returns 
 */
export const withWidget = (widgetName = '') => FormComponent => WidgetIcon => ({ ...props }) => {
  const classes = useStyles();
  const dispatch = useAppDispatch();
  const boxUIState = useTypedReduxSelector(state => state.boxPageUI);
  const widgetUIState = useTypedReduxSelector(state => state.widgetUI);

  const handlewidgetChoosed = () => {
    dispatch(setBoxUINewWidgetChoosed({ state: true, name: widgetName }));
    dispatch(setWidgetId(props.widgetId));
    dispatch(setWidgetSelfVariant(widgetName));
  }

  //Reset to catalog
  useEffect(() => {
    dispatch(setBoxUINewWidgetChoosed({ state: false, name: '' }));
    return () => dispatch(setBoxUINewWidgetChoosed({ state: false, name: '' }));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <React.Fragment>
      <Grid item xs={12} md={boxUIState.isNewWidgetChoosed.state && boxUIState.isNewWidgetChoosed.name === widgetName ? 12 : 4} lg={boxUIState.isNewWidgetChoosed.state && boxUIState.isNewWidgetChoosed.name === widgetName ? 12 : 4}>
        <Card className={
          boxUIState.isNewWidgetChoosed.state && boxUIState.isNewWidgetChoosed.name === widgetName ?
            classes.widgetCatalogCardChoosed
            : boxUIState.isNewWidgetChoosed.state && boxUIState.isNewWidgetChoosed.name !== widgetName ?
              classes.unChoosedWidget : classes.widgetCatalogCard}>
          {boxUIState.isNewWidgetChoosed.state && boxUIState.isNewWidgetChoosed.name === widgetName ? <CardHeader title="Add new widget" subheader={widgetName} /> : null}
          <CardContent
            className={
              clsx({
                [classes.choosedWidget]: boxUIState.isNewWidgetChoosed.state && boxUIState.isNewWidgetChoosed.name === widgetName,
              })
            }
            onClick={handlewidgetChoosed}>
            <Grid container direction="column" justify="center" alignItems="center" spacing={1}>
              {boxUIState.isNewWidgetChoosed.state && boxUIState.isNewWidgetChoosed.name === widgetName ? (
                <WidgetForm>
                  {FormComponent !== null ? <FormComponent /> : null}
                </WidgetForm>
              )
                : (
                  <React.Fragment>
                    <Grid item xs={12}>
                      <WidgetIcon />
                    </Grid>
                    <Grid item xs={12}>
                      {widgetName}
                    </Grid>
                  </React.Fragment>
                )}

            </Grid>
          </CardContent>
          {boxUIState.isNewWidgetChoosed.state ? <ChoosedAction type={widgetName} /> : null}
        </Card>
      </Grid>
    </React.Fragment>
  );
}

function WidgetForm(props) {
  const dispatch = useAppDispatch();
  const appState = useTypedReduxSelector(state => state.app);
  const boxState = useTypedReduxSelector(state => state.box);
  const widgetState = useTypedReduxSelector(state => state.widget);
  const widgetUIState = useTypedReduxSelector(state => state.widgetUI);
  const classes = useStyles();

  const handleWidgetTitle = (e) => {
    dispatch(setWidgetTitle(e.target.value));
  }

  const handleWidgetSubTitle = (e) => {
    dispatch(setWidgetSubtitle(e.target.value));
  }

  const handleDataSourceSelection = (e) => {
    dispatch(setWidgetDataSource(e.target.value));
  }

  const handleDataValue = (e) => {
    dispatch(setWidgetDataValue(e.target.value));
  }

  const generateDefaultWidgetLayout = () => {
    dispatch(setWidgetLayout({
      w: 4,
      h: 6,
      i: Date.now().toString(),
      minW: 2,
      minH: 6,
      x: (boxState.boxWidgets.length * 2) % (boxState.boxCols || 12),
      y: 0,
    }));
    dispatch(setWidgetIndexLayout(Date.now()));
  }

  useEffect(() => {
    generateDefaultWidgetLayout();
    return () => {
      dispatch(setWidgetId(0));
      dispatch(resetWidgetSlice());
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);


  return (
    <React.Fragment>
      <FormControl>
        <form className={classes.fieldFlex}>
          <Grid container spacing={3}>
            <Grid item xs={12} md={12} lg={12}>
              <TextField required onBlur={handleWidgetTitle} variant="outlined" id="widget-title" label="Widget Title" aria-describedby="widget-title-error" />
              <FormHelperText id="widget-title-error">This field is required</FormHelperText>
            </Grid>
            <Grid item xs={12} md={12} lg={12}>
              <TextField required onBlur={handleWidgetSubTitle} variant="outlined" id="widget-subtitle" label="Widget Subtitle" />
            </Grid>


            {/* DataField Section */}
            {!widgetUIState.useOwnDataField &&
              <Grid item xs={12} md={12} lg={12}>
                <FormControl variant="outlined" style={{ minWidth: 220 }}>
                  <InputLabel id="data-source-selection-input">Data Source</InputLabel>
                  <Select
                    labelId="data-source-selection"
                    id="data-source-selection"
                    onChange={handleDataSourceSelection}
                    label="Data Source"
                  >
                    <MenuItem value={null}>
                      <em>None</em>
                    </MenuItem>
                    <MenuItem value={'manual'}>
                      <em>Manual</em>
                    </MenuItem>
                    <MenuItem value={'datafield'}>
                      <em>Data Field</em>
                    </MenuItem>

                  </Select>
                </FormControl>
                <FormControl required variant="outlined" style={{ minWidth: 220, marginLeft: 20, display: widgetState.data?.source === 'datafield' ? null : 'none' }}>
                  <InputLabel id="widget-data-field-value">Field Name</InputLabel>
                  <Select
                    labelId="field-value-type"
                    id="demo-simple-select-outlined"
                    onChange={handleDataValue}
                    label="Field Name"
                  >
                    {
                      boxState.boxSettings.dataField?.length !== 0 ?
                        boxState.boxSettings.dataField.map((field, i) => (
                          <MenuItem key={i} value={field}>{field}</MenuItem>
                        ))
                        : <MenuItem value={0}><em>None</em></MenuItem>
                    }
                  </Select>
                </FormControl>
                <FormControl variant="outlined" style={{ minWidth: 220, marginLeft: 20, display: widgetState.data?.source === 'manual' ? null : 'none' }}>
                  <TextField onBlur={handleDataValue} variant="outlined" id="widget-data-manual-value" label="Data Manual Value" />
                </FormControl>
              </Grid>
            }
            {/* End Of DataField Section */}

            <Grid item xs={12} md={12} lg={12}>
              <Typography variant="subtitle2" gutterBottom>
                Widget options
              </Typography>
            </Grid>
            {/* Optional form goes here */}
            {props.children}
          </Grid>
        </form>
      </FormControl>
    </React.Fragment>
  )
}

function ChoosedAction({ type }) {
  const dispatch = useAppDispatch();
  const appState = useTypedReduxSelector(state => state.app);
  const boxState = useTypedReduxSelector(state => state.box);
  const widgetState = useTypedReduxSelector(state => state.widget);

  const handleCancelButton = () => {
    dispatch(setBoxUINewWidgetChoosed({state: false, name: ''}));
  }

  const handleWidgetSave = async () => {
    dispatch(setFetchingStatus(true));

    try {
      const dataWidget = {
        boxToken: boxState.boxToken,
        widgetUniqueId: widgetState.widgetId,
        widgetProperties: JSON.stringify(widgetState.properties),
        widgetLayout: JSON.stringify(widgetState.layout),
        widgetIndexLayout: `${widgetState.layout.i}`,
        widgetOptions: JSON.stringify(widgetState.options),
        widgetData: JSON.stringify(widgetState.data),
      }

      const saveResult = await api.saveWidgetOnBox(dataWidget, apiCancelToken);

      const widgetCreator = {
        data: widgetState.data,
        layout: widgetState.layout,
        options: widgetState.options,
        properties: widgetState.properties,
        self: { type: null, variant: type },
        widgetId: saveResult.data.data.id_box_widget,
      }

      dispatch(addBoxNewWidget(widgetCreator));
      dispatch(setFetchingState({
        message: 'Congratulations! Widget has been added to your box!',
        severity: 'success',
        isOpen: true,
        fetchingStatus: false,
      }));
    } catch (error) {
      console.error('Add Widget', error);
      dispatch(setFetchingState({
        message: 'Somethings went wrong while adding widget to your box!',
        severity: 'error',
        isOpen: true,
        fetchingStatus: false,
      }));
    }
  }


  return (
    <React.Fragment>
      <CardActions>
        <Button onClick={() => {
          handleWidgetSave();
        }
        } size="small" color="primary">
          {appState.fetchingStatus ? <CircularProgress size={35} /> : 'Save'}
        </Button>
        <Button onClick={() => handleCancelButton()} size="small" color="primary">
          Cancel
        </Button>
      </CardActions>
    </React.Fragment>
  );
}