import { Grid, Typography } from '@material-ui/core';
import React from 'react';


export default function HeaderPage() {
  return (
    <Grid container justify="space-between" alignItems="center" direction="row">
      <Grid item xs={8} md={10} lg={10}>
        <Typography variant="h4" component="h2">
          {/* {box.boxTitle === '' ? (<Loading width={400} height={40} />) : 'Settings'} */}
          Account
        </Typography>
        <Typography variant="subtitle1" component="h4" color="textSecondary">
          {/* {box.boxSubTitle === '' ? (<Loading width={300} height={20} />) : box.boxTitle} */}
          Setting for your account
        </Typography>
      </Grid>
    </Grid>
  )
}