import produce from 'immer';
import { useCallback } from 'react';
import { useState } from 'react';
import { createContainer } from 'react-tracked';

const initialState = {
  // Widget Editing Resource
  widgetEditStatus: false,
  isAddNewWidget: false,
  widgetIndex: 1,
  widgetUniqueId: 0,
  widgetTitle: '',
  widgetSubTitle: '',
  widgetFieldName: '',
  widgetMetricUnit: '',
  widgetMaxValue: 100,
  widgetSlot: 0,
  widgetSequence: 0,
  widgetOptions: {},

  //AddWidgetSection
  isChoosed: {
    state: false,
    name: '',
  },

  //Widget Data Section 
  WidgetDataContainer: {},

  //Widget Container
  selectedWidget: [],
  fetchingMessage: { message: '', severity: '', isOpen: false },
  fetchingStatus: false,


  /**
   *  --------------
   *  State based on databox from API
   *  --------------
   */
  widgetId: 0,
  properties: {},
  layout: {
    x: 0,
    y: 0,
    w: 0,
    h: 0,
    minW: 2,
    minH: 9,
    i: 0
  },
  data:{},
  options: {},
  self: {},

   /**
   *  --------------
   *  State based on widgets from API
   *  --------------
   */

  widgetIndexLayout: 0,

  /**
   *  --------------
   *  Derivative state databox from API
   *  --------------
   */
  widgetsLayout: [],

  useOwnDatafield: false,

  //self
  widgetType: '',
  widgetVariant: '',
};

const useAllWidgetState = () => useState(initialState);


export const {
  Provider: WidgetStateProvider,
  useTracked: useWidgetState,
  useUpdate: useWidgetSetState,
  useTrackedState: useWidgetStateValue,
} = createContainer(useAllWidgetState);

export const useSetWidgetDraft = () => {
  const setWidgetState = useWidgetSetState();
  return useCallback(
    (draftUpdater) => {
      setWidgetState(produce(draftUpdater));
    },
    [setWidgetState],
  );
};
