import { FormControl, Grid, InputLabel, MenuItem, Select, TextField, Tooltip, Typography } from '@material-ui/core';
import useStyles from '@pages/BoxPage/Components/Styles';
import { useWidgetState } from '@pages/BoxPage/Context/widgetContextTracked';
import { setWidgetOption, setWidgetProperties } from '@pages/BoxPage/Redux/WidgetSlice';
import { useAppDispatch, useTypedReduxSelector } from '@store/Store';
import React, { useEffect, useState } from 'react';
import { SketchPicker } from 'react-color';

export default {
  FormLabel: WidgetFormLabel,
  ColorPicker: WidgetColorPicker,
  MinMaxValue: WidgetMinMaxValue,
  MetricUnit: WidgetMetricUnit,
  Orientation: WidgetFormOrientation,
}

/**
 * Widget Form for determine Min-Max Value range 
 *  
 * 
 * @param {any} props 
 * @returns {JSX} React Element
 */
function WidgetMetricUnit(props) {
  const dispatch = useAppDispatch();

  const handleMetricChange = (e) => {
    dispatch(
      setWidgetProperties({
        metric: e.target.value,
      })
    );
  }

  return (
    <React.Fragment>
      <Grid item xs={12} md={12} lg={12}>
        <Tooltip title={props.helperText} arrow>
          <TextField onBlur={handleMetricChange} variant="outlined" id="widget-metric-unit" label="Widget Metric Unit" />
        </Tooltip>
      </Grid>
    </React.Fragment>
  );
}

WidgetMetricUnit.defaultProps = {
  helperText: 'This could be C for Celcius. Or F for Fahrenheit, or else. Even only percentage'
}


/**
 * Widget Form for determine Min-Max Value range 
 *  
 * 
 * @param {any} props 
 * @returns {JSX} React Element
 */
function WidgetMinMaxValue(props) {
  const dispatch = useAppDispatch();
  const widgetState = useTypedReduxSelector(state => state.widget);

  const handleMinValue = (value) => {
    dispatch(
      setWidgetOption({
        minValue: parseInt(value, 10),
      })
    );
  }

  const handleMaxValue = (value) => {
    dispatch(
      setWidgetOption({
        maxValue: parseInt(value, 10),
      })
    );
  }

  return (
    <React.Fragment>
      <Grid item xs={12} md={12} lg={12}>
        <Typography variant="caption" display="block" color="textSecondary">
          {props.captionText}
        </Typography>
      </Grid>
      <Grid item xs={12} md={12} lg={12}>
        <FormControl variant="outlined" style={props.style || { minWidth: 220 }}>
          <Tooltip title={props.helperText} arrow>
            <TextField value={widgetState.options?.minValue || null} type="number" onChange={(event) => handleMinValue(event.target.value)} variant="outlined" id="minimum-value-range" label="Minimum Value Range" InputLabelProps={{ shrink: true }} />
          </Tooltip>
        </FormControl>
        <FormControl variant="outlined" style={props.style || { minWidth: 220, marginLeft: 20 }}>
          <Tooltip title={props.helperText} arrow>
            <TextField value={widgetState.options?.maxValue || null} type="number" onChange={(event) => handleMaxValue(event.target.value)} variant="outlined" id="maximum-value-range" label="Maximum Value Range" InputLabelProps={{ shrink: true }} />
          </Tooltip>
        </FormControl>
      </Grid>
    </React.Fragment>
  );
}

WidgetMinMaxValue.defaultProps = {
  captionText: null,
  style: null,
  helperText: 'This value will be your range value. If widget use percentage to display the value, data value will be re-mapped to this range'
}

/**
 * Widget Form for color change. Especially for background
 * 
 * 
 * @param {any} props 
 * @returns {JSX} React Element
 */
function WidgetColorPicker(props) {
  const dispatch = useAppDispatch();
  const widgetState = useTypedReduxSelector(state => state.widget);
  const [isPickerShow, setPickerShow] = useState(false);
  const classes = useStyles();

  const handleColorChange = color => {
    dispatch(
      setWidgetOption({
        color: color.hex,
      })
    )
  }

  const handleColorFieldChange = e => {
    dispatch(
      setWidgetOption({
        color: e.target.value,
      })
    )
  }

  useEffect(() => {
    dispatch(
      setWidgetOption({
        color: props.hexColor,
      })
    )
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <React.Fragment>
      <Grid item xs={12} md={12} lg={12}>
        <Tooltip title={props.helperText} arrow>
          <TextField value={widgetState.options?.color || props.hexColor} onChange={handleColorFieldChange} onFocus={() => setPickerShow(true)} variant="outlined" id="background-color" label="Background Color" />
        </Tooltip>
      </Grid>
      {isPickerShow && <SketchPicker
        color={widgetState.options?.color}
        onChange={handleColorChange}
        onChangeComplete={() => setPickerShow(false)}
        className={classes.colorPicker}
      />}
    </React.Fragment>
  );
}

WidgetColorPicker.defaultProps = {
  labelName: 'background',
  hexColor: '#ffffff',
  helperText: 'If you want to change background color on widget card panel',
}

/**
 * Widget Form for label. Single or multiple
 * 
 * 
 * @param {any} props 
 * @returns {JSX} React Element
 */
function WidgetFormLabel(props) {
  const dispatch = useAppDispatch();

  const handleLabelChange = e => {
    dispatch(
      setWidgetOption({
        label: e.target.value,
      })
    )
  }

  return (
    <React.Fragment>
      {props.isMultiLabel ? null : (
        <Grid item xs={12} md={12} lg={12}>
          <Tooltip title={props.helperText} arrow>
            <TextField onBlur={handleLabelChange} variant="outlined" id="label-name" label="Label Name" />
          </Tooltip>
        </Grid>
      )}
    </React.Fragment>
  );
}

WidgetFormLabel.defaultProps = {
  isMultiLabel: false,
  numOfLabels: 1,
  helperText: 'Used for renaming widget that different with field name',
}

/**
 * Widget Form for label. Single or multiple
 * 
 * 
 * @param {any} props 
 * @returns {JSX} React Element
 */
function WidgetFormOrientation(props) {
  const dispatch = useAppDispatch();
  const [defaultOrientation, setOrientation] = useState('horizontal');

  const handleOrientationChange = e => {
    dispatch(
      setWidgetOption({
        orientation: e.target.value,
      })
    )
    setOrientation(e.target.value);
  }

  return (
    <React.Fragment>
      <Grid item xs={12} md={12} lg={12}>
        <FormControl required variant="outlined" style={{ minWidth: 220, width: '100%' }}>
          <InputLabel id="widget-orientation">Widget Orientation</InputLabel>
          <Select
            value={defaultOrientation}
            labelId="widget-orientation"
            id="widget-orientation-selection"
            onChange={handleOrientationChange}
            label="Widget Orientation"
          >
            <MenuItem value={'horizontal'}>Horizontal</MenuItem>
            <MenuItem value={'vertical'}>Vertical</MenuItem>
          </Select>
        </FormControl>
      </Grid>
    </React.Fragment>
  );
}

WidgetFormOrientation.defaultProps = {
  orientation: 'horizontal'
}