import { AuthContext } from '@globalContext/AuthContext';
import DashboardImage from '@globalImages/dashboard-image.svg';
import { Box, Container, useScrollTrigger } from '@material-ui/core';
import CssBaseline from '@material-ui/core/CssBaseline';
import { makeStyles } from '@material-ui/core/styles';
import ApiService, { generateCancelToken } from '@services/apiService';
import clsx from 'clsx';
import React, { useContext, useEffect, useState } from 'react';
import { useLocation } from 'react-router';
import DrawerMenu from './Components/Drawer';
import Header from './Components/Header';
import { setAppReRender, setUserBox } from './Redux/AppSlice';
import { useAppDispatch, useTypedReduxSelector } from './Redux/Store';


const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
  },
  whiteColor: {
    color: '#FFFFFF',
  },
  content: {
    flexGrow: 1,
    height: '100vh',
    overflow: 'auto',
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    marginLeft: -240,
  },
  contentShift: {
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
    marginLeft: 0,
  },
  container: {
    paddingTop: theme.spacing(4),
    paddingBottom: theme.spacing(4),
  },
  appBarSpacer: theme.mixins.toolbar,
  dashboard: {
    backgroundImage: `url(${DashboardImage})`,
    backgroundColor: '#1976D2',
    backgroundRepeat: 'no-repeat',
    backgroundPosition: '50% 30%',
    backgroundSize: 'cover',
    position: 'relative',
    top: -50,
    height: 350,
  },

}));

const apiCancelToken = generateCancelToken();
const api = new ApiService(apiCancelToken);

export default function ContainerApps(props) {
  const classes = useStyles();
  const location = useLocation();
  const appState = useTypedReduxSelector(state => state.app);

  // useEffect(() => {
  //   if (appState.boxes.length === 0) {
  //     if (auth.userName !== undefined && auth.isLoggedIn) {
  //       api.getBoxUser()
  //         .then(({ data }) => {
  //           let userData = data.data;
  //           dispatch(setUserBox(userData.boxes));
  //         })
  //         .then(() => {
  //           dispatch(setAppReRender(true));
  //         });
  //     }
  //   }

  //   return () => {
  //     apiCancelToken.cancel();
  //   }

  //   // eslint-disable-next-line react-hooks/exhaustive-deps
  // }, [auth.isLoggedIn])



  return (
    <div className={classes.root}>
      <CssBaseline />
      <DrawerMenu />
      <main className={clsx(classes.content, {
        [classes.contentShift]: appState.isDrawerOpen
      })}>
        <Header {...props} optStyles={classes.whiteColor} />
        {location.pathname === '/dashbox' ? (<BoxImage />) : null}
        <Container className={classes.container}>
          {props.children}
        </Container>
      </main>
    </div>
  )
}

function BoxImage() {
  const classes = useStyles();
  const [offset, setOffset] = useState(0);
  const trigger = useScrollTrigger({
    disableHysteresis: true,
    threshold: 0,
    target: window ? window : undefined,
  });


  useEffect(() => {
    function handleScroll() {
      setOffset(window.pageYOffset);
    }

    window.addEventListener("scroll", handleScroll);

    return () => {
      window.removeEventListener("scroll", handleScroll);
    };

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [trigger]);

  return <Box style={{ transform: `translateY(${offset * 0.2}px)` }} component="div" className={classes.dashboard} />;
}