export function generateCancelToken() {
  return axios.CancelToken.source();
}

export default class ApiService {
  constructor(canceltoken) {
    if (canceltoken === undefined) {
      this.canceltoken = generateCancelToken();
    }
    this.canceltoken = canceltoken;
    this.http = axios.create({
      baseURL: CODERSBOX_URL,
      withCredentials: true,
      xsrfCookieName: 'XSRF-TOKEN',
      xsrfHeaderName: 'X-XSRF-TOKEN',
      cancelToken: this.canceltoken.token,
    });

    this.http.interceptors.response.use(response => response, error => {
      if (error.response?.status === 401) {
        Cookie.remove(COOKIE_LOGIN_STATE, { expires: 1, sameSite: 'Lax' });
        window.location.href = '/login';
        Promise.reject();
      }

      return Promise.reject(error);
    });
  }

  /**
 * 
 * General
 * 
 */
  fetchAll([...args]) {
    return axios.all([...args]);
  }

  /**
 * 
 *  GET
 * 
 */
  getBox(token, isWidget = true, canceltoken = '') {
    if (isWidget) {
      return this.http.get(`${API_BOX_URL}/${token}?widgets=1&settings=1`);
    }
    return this.http.get(`${API_BOX_URL}/${token}`);
  }

  getBoxData(token, withFeed = false, canceltoken = '') {
    if (withFeed) {
      return this.http.get(`${API_BOX_URL}/${token}/data/get?withFeed=${withFeed}`);
    }
    return this.http.get(`${API_BOX_URL}/${token}/data/get`);
  }

  getBoxSettings(token) {
    return this.http.get(`${API_BOX_URL}/${token}/settings`);
  }

  getWidgetOnBox(idWidget, token) {
    return this.http.get(`${API_BOX_URL}/${token}/widgets/${idWidget}`)
  }

  getConnectionList() {
    return this.http.get(`${API_BOX_URL}/connection/list`);
  }

  getBoxUser() {
    return this.http.get(`${API_BOX_URL}`);
  }

  getUserCookie() {
    return this.http.get(`${CODERSBOX_URL}/api/set/csrf-cookie`);
  }

  getCodersProfile(userName) {
    return this.http.get(`${API_URL}/user/coders/${userName}`);
  }

  getBoxTrash() {
    return this.http.get(`${API_BOX_URL}/trash`);
  }

  getBoxAPI() {
    return this.http.get(`${API_BOX_URL}/boxapi`);
  }

  getBoxTrashRestore(boxToken) {
    return this.http.get(`${API_BOX_URL}/${boxToken}/restore`);
  }

  getBoxDataCodersbox(boxToken) {
    return this.http.get(`${API_BOX_URL}/${boxToken}/data`);
  }

  getBoxTrashDelete(boxToken, boxDeviceToken) {
    return this.http.get(`${API_BOX_URL}/${boxToken}/${boxDeviceToken}/remove`);
  }

  userLogout() {
    return this.http.get(`${API_URL}/logout`);
  }

  /**
 * 
 *  POST
 * 
 */

  saveWidgetBoxSettings(data, canceltoken = '') {
    return this.http.post(`${API_WIDGET_URL}/box/addnew`, data);
  }

  saveWidgetOnBox(data, canceltoken = '') {
    return this.http.post(`${API_BOX_URL}/widgets`, data);
  }

  saveNewBox(data, canceltoken = '') {
    return this.http.post(`${API_BOX_URL}`, data);
  }

  saveNewUser(data, canceltoken = '') {
    return this.http.post(`${CODERSBOX_URL}/signup`, data);
  }

  saveDataFromDashboard(boxToken,data) {
    return this.http.post(`${API_BOX_URL}/${boxToken}/data`, data);
  }

  findUser(data, canceltoken = '') {
    // return this.http.post(`${CODERSBOX_URL}/api/v2/user/login`, data);
    return this.http.post(`${API_URL}/login`, data);
  }

  userAutoLoginCheck(token, canceltoken = '') {
    return this.http.get(`${API_URL}/usercheck`);
  }

  /**
   * 
   *  UPDATE
   * 
   */
  updateBox(token, data, canceltoken = '') {
    return this.http.patch(`${API_BOX_URL}`, data);
  }

  updateBoxSettings(token, data, canceltoken = '') {
    return this.http.patch(`${API_BOX_URL}/settings`, data);
  }

  updateWidgetBoxSettings(data, canceltoken = '') {
    return this.http.patch(`${API_BOX_URL}/widgets`, data);
  }

  updateWidgetBoxLayout(data, canceltoken = '') {
    return this.http.patch(`${API_BOX_URL}/widgets?update=layout`, data);
  }

  updateWidgetCount(data, cancelToken = '') {
    return this.http.patch(`${API_WIDGET_URL}/user/update`, data);
  }

  updateUserPassword(data) {
    return this.http.patch(`${API_URL}/users/password`, data);
  }

  updateUserTelegramID(data) {
    return this.http.patch(`${API_URL}/users/telegram`, data);
  }

  updateAll([...args]) {
    return axios.all([...args]);
  }


  /**
 * 
 *  DELETE
 * 
 */
  deleteWidgetOnBox(idWidget, token) {
    return this.http.delete(`${API_BOX_URL}/${token}/widgets/${idWidget}`);
  }

  deleteBox(token) {
    return this.http.delete(`${API_BOX_URL}/${token}`);
  }
}
