import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';

/** Forms */
import FormBoxName from './formBoxName';
import FormDataField from './formDataField';

/** Icons */
import BoxIcon from '@material-ui/icons/AllInbox';
import FieldIcon from '@material-ui/icons/List';
import DataConnection from '@material-ui/icons/SettingsInputComponent';
import FormConnection from './formConnection';


function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`scrollable-auto-tabpanel-${index}`}
      aria-labelledby={`scrollable-auto-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `scrollable-auto-tab-${index}`,
    'aria-controls': `scrollable-auto-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    width: '100%',
    backgroundColor: theme.palette.background.paper,
    marginTop: 20,
  },
}));

export default function SettingsTabs() {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <div className={classes.root}>
      <AppBar position="static" color="default">
        <Tabs
          value={value}
          onChange={handleChange}
          indicatorColor="primary"
          textColor="primary"
          variant="scrollable"
          scrollButtons="on"
          aria-label="scrollable auto tabs example"
        >
          <Tab icon={<BoxIcon />} label="Box" {...a11yProps(0)} />
          <Tab icon={<FieldIcon />}label="Data Field" {...a11yProps(1)} />
          <Tab icon={<DataConnection />} label="Connection" {...a11yProps(2)} />
        </Tabs>
      </AppBar>
      <TabPanel value={value} index={0}>
        <FormBoxName />
      </TabPanel>
      <TabPanel value={value} index={1}>
        <FormDataField />
      </TabPanel>
      <TabPanel value={value} index={2}>
        <FormConnection />
      </TabPanel>
    </div>
  );
}
