import React, { useEffect, useState, useRef } from 'react';
import mapboxgl from 'mapbox-gl';
import 'mapbox-gl/dist/mapbox-gl.css';
import useStyles from './Styles';
import useWidgetDataHandler from '@pages/BoxPage/hooks/WidgetDataHandler';
mapboxgl.accessToken = "pk.eyJ1IjoiZGltYXNiZGcyMSIsImEiOiJja3JuaGEyODQxdDhjMm9ydmJtcWxnZnJ0In0.UfDYxhu-L9LP9WqiLcI53g";

export default function MapWidgetComponent({ widgetId, widgetProperties, widgetData, widgetOptions }) {
  const classes = useStyles();
  const [data, setData] = useWidgetDataHandler('Maps', widgetData, widgetOptions, widgetId, [106.8731, -6.1837]);
  const defaultLong = 106.8731;
  const defaultLat = -6.1837;
  const mapContainerRef = useRef(null);
  const [zoom, setZoom] = useState(12.00);

  // Initialize map when component mounts
  useEffect(() => {

    let dataLong = data[widgetData.value[0]];
    let dataLat = data[widgetData.value[1]];

    if(
      (dataLong === undefined && dataLat === undefined)
      || (dataLong === 0 && dataLat === 0)
    ) 
    {
      dataLong = defaultLong;
      dataLat = defaultLat;
    }

    console.log('maps', data);
    const map = new mapboxgl.Map({
      container: mapContainerRef.current,
      style: 'mapbox://styles/mapbox/streets-v11',
      center: [defaultLong, defaultLat],
      zoom: zoom
    });

    // Add navigation control (the +/- zoom buttons)
    map.addControl(new mapboxgl.NavigationControl(), 'top-right');

    const marker = new mapboxgl.Marker().setLngLat([dataLong, dataLat]).addTo(map);

    map.on('move', () => {
      map.getCenter().lng.toFixed(4);
      map.getCenter().lat.toFixed(4);
      setZoom(map.getZoom().toFixed(2));
    });

    // Clean up on unmount
    return () =>{ 
      map.remove();
    };
  }, [data]);

  return (
    <React.Fragment>
      <div className={classes.sidebar}>
        Longitude: {data[widgetData.value[0]]} | Latitude: {data[widgetData.value[1]]} | Zoom: {zoom}
      </div>
      <div ref={mapContainerRef} className={classes.mapContainer} />
    </React.Fragment>
  )
}