import { Grid, Typography } from '@material-ui/core';
import useWidgetDataHandler from '@pages/BoxPage/hooks/WidgetDataHandler';
import React from 'react';

export default function DisplayWidgetComponent({ widgetId, widgetProperties, widgetData, widgetOptions }) {
  const [data, ] = useWidgetDataHandler('Display', widgetData, widgetOptions, widgetId);

  return (
    <React.Fragment>
      <Grid container direction="column" justify="center" alignItems="center">
        <Grid item>
          <Typography component="p" variant="h2">
            {data}
          </Typography>
        </Grid>
        <Grid item>
          <Typography color="textSecondary">
            {widgetProperties.metric}
          </Typography>
        </Grid>
      </Grid>
    </React.Fragment>
  )
}