import React from 'react';
import { Grid } from '@material-ui/core';


import ChartLineWidget from '../widgets/ChartLine';
import ChartRadialWidget from '../widgets/ChartRadial';
import ButtonWidget from '../widgets/Button';
import LedWidget from '../widgets/Led';
import DisplayWidget from '../widgets/Display';
import SliderWidget from '../widgets/Slider';
import MapWidget from '../widgets/Map';
import StepperWidget from '../widgets/Stepper';


export const choosenWidget = (widgetId, widgetVariant, widgetProperties, widgetData, widgetOptions) => {

  if (widgetVariant === "radial") {
    return <ChartRadialWidget.Component 
                                widgetId={widgetId} 
                                widgetData={widgetData} 
                                widgetProperties={widgetProperties} 
                                widgetOptions={widgetOptions}/>
  }

  if (widgetVariant === "button") {
    return <ButtonWidget.Component 
                                widgetId={widgetId} 
                                widgetData={widgetData} 
                                widgetProperties={widgetProperties} 
                                widgetOptions={widgetOptions}/>
  }

  if (widgetVariant === "display") {
    return <DisplayWidget.Component 
                                widgetId={widgetId} 
                                widgetData={widgetData} 
                                widgetProperties={widgetProperties} 
                                widgetOptions={widgetOptions}/>
  }

  if (widgetVariant === "line") {
    return <ChartLineWidget.Component 
                                widgetId={widgetId} 
                                widgetData={widgetData} 
                                widgetProperties={widgetProperties} 
                                widgetOptions={widgetOptions}/>
  }

  if (widgetVariant === "slider") {
    return <SliderWidget.Component 
                                widgetId={widgetId} 
                                widgetData={widgetData} 
                                widgetProperties={widgetProperties} 
                                widgetOptions={widgetOptions}/>
  }

  if (widgetVariant === "map") {
    return <MapWidget.Component 
                                widgetId={widgetId} 
                                widgetData={widgetData} 
                                widgetProperties={widgetProperties} 
                                widgetOptions={widgetOptions}/>
  }

  if (widgetVariant === "led") {
    return <LedWidget.Component 
                                widgetId={widgetId} 
                                widgetData={widgetData} 
                                widgetProperties={widgetProperties} 
                                widgetOptions={widgetOptions}/>
  }

  if (widgetVariant === "stepper") {
    return <StepperWidget.Component 
                                widgetId={widgetId} 
                                widgetData={widgetData} 
                                widgetProperties={widgetProperties} 
                                widgetOptions={widgetOptions}/>
  }

}

export default function WidgetsCatalog(props) {
  return (
    <Grid container direction="row" justify="center" alignItems="center" spacing={1}>
      <ChartLineWidget.Form widgetId={2} />
      <ButtonWidget.Form widgetId={5}/>
      <ChartRadialWidget.Form widgetId={4}/>
      <DisplayWidget.Form widgetId={1}/>
      <SliderWidget.Form widgetId={7}/>
      <MapWidget.Form widgetId={8} />
      <LedWidget.Form widgetId={9} />
      <StepperWidget.Form widgetId={10} />
    </Grid>
  );
}