import { useAppState } from '@globalContext/AppContextTracked';
import { Chip, IconButton, InputAdornment, TextField, Typography } from '@material-ui/core';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import CancelIcon from '@material-ui/icons/Cancel';
import CheckIcon from '@material-ui/icons/Check';
import FileCopyIcon from '@material-ui/icons/FileCopy';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import Skeleton from '@material-ui/lab/Skeleton';
import ApiService, { generateCancelToken } from '@services/apiService';
import { setFetchingState, setFetchingStatus } from '@store/AppSlice';
import { useAppDispatch, useTypedReduxSelector } from '@store/Store';
import React, { useEffect, useState } from 'react';

const useStyles = makeStyles({
  table: {
    minWidth: 650,
  },
});



export default function BasicTable() {
  const apiCancelToken = generateCancelToken();
  const api = new ApiService(apiCancelToken);
  const dispatch = useAppDispatch();
  const appState = useTypedReduxSelector(state => state.app);
  const classes = useStyles();
  const [boxTrashState, setBoxTrashState] = useState([]);
  const [visibilityContent, setVisbility] = useState(false);

  const fetchBoxTrash = async () => {
    try {
      dispatch(setFetchingStatus(true));
      const response = await api.getBoxAPI();
      const boxData = response.data.data;
      setBoxTrashState(boxData);
      dispatch(setFetchingState({
        message: 'Box Trash load successfully',
        isOpen: true,
        severity: 'success',
        fetchingStatus: false,
      }));
    } catch (error) {
      console.error('API Page', error);
      dispatch(setFetchingState({
        message: 'Box Trash load failed!',
        isOpen: true,
        severity: 'error',
        fetchingStatus: false,
      }));
    }
  }

  useEffect(() => {
    fetchBoxTrash();
    return () => {
      if (boxTrashState.length !== 0) {
        apiCancelToken.cancel();
      }
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <Grid item lg={12} md={12} xs={12}>
      <TableContainer component={Paper}>
        <Table className={classes.table} aria-label="Box Trash">
          <TableHead>
            <TableRow>
              <TableCell>Box Name</TableCell>
              <TableCell align="center">Box Unique Token</TableCell>
              <TableCell align="center">Device Unique Token</TableCell>
              <TableCell align="center">Status</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {boxTrashState.length > 0 ?
              boxTrashState.map(box => (
                <TableRow hover key={box.boxToken}>
                  <TableCell component="th" scope="row">
                    {box.boxName}
                  </TableCell>
                  <TableCell align="center">
                    <TextField
                      value={box.boxToken}
                      type={visibilityContent ? "text" : "password"}
                      variant="outlined"
                      InputProps={{
                        endAdornment: <InputAdornment position="end">
                          <IconButton onClick={() => setVisbility(state => state = !state)}>
                            {visibilityContent ? <Visibility /> : <VisibilityOff />}
                          </IconButton>
                          <IconButton onClick={() => { navigator.clipboard.writeText(box.boxToken) }}>
                            <FileCopyIcon />
                          </IconButton>
                        </InputAdornment>
                      }} />
                  </TableCell>
                  <TableCell align="center">
                    <TextField
                      value={box.boxDeviceToken}
                      type={visibilityContent ? "text" : "password"}
                      variant="outlined"
                      InputProps={{
                        endAdornment: <InputAdornment position="end">
                          <IconButton onClick={() => setVisbility(state => state = !state)}>
                            {visibilityContent ? <Visibility /> : <VisibilityOff />}
                          </IconButton>
                          <IconButton onClick={() => { navigator.clipboard.writeText(box.boxDeviceToken) }}>
                            <FileCopyIcon />
                          </IconButton>
                        </InputAdornment>
                      }} />
                  </TableCell>
                  <TableCell align="center">{box.boxStatus === 'active' ?
                    <Chip label={box.boxStatus} color="primary" size="small" icon={<CheckIcon />} />
                    :
                    <Chip label={box.boxStatus} color="secondary" size="small" icon={<CancelIcon />} />
                  }</TableCell>
                </TableRow>
              )) : appState.fetchingStatus ? (
                <TableRow>
                  <TableCell component="th" scope="row">
                    <Skeleton animation="wave" />
                  </TableCell>
                  <TableCell align="center"><Skeleton animation="wave" /></TableCell>
                  <TableCell align="center"><Skeleton animation="wave" /></TableCell>
                  <TableCell align="center"><Skeleton animation="wave" /></TableCell>
                  <TableCell align="center"><Skeleton animation="wave" /></TableCell>
                </TableRow>
              ) : (
                <TableRow>
                  <TableCell colSpan={5} align="center">
                    <Typography variant="subtitle1" component="h4" color="textSecondary">
                      No Box Trashed
                    </Typography>
                  </TableCell>
                </TableRow>
              )}
          </TableBody>
        </Table>
      </TableContainer>
    </Grid>
  );
}