import { AuthContext } from '@globalContext/AuthContext';
import {
  Button,
  Dialog, DialogActions, DialogContent, DialogContentText,
  DialogTitle, Fab, Grid, TextField, useMediaQuery,
  useTheme
} from '@material-ui/core';
import AddIcons from '@material-ui/icons/Add';
import ApiService, { generateCancelToken } from '@services/apiService';
import { setFetchingState, setFetchingStatus } from '@store/AppSlice';
import { useAppDispatch, useTypedReduxSelector } from '@store/Store';
import React, { useContext } from 'react';
import { setAddNewBox, setNewBoxCreator, setUserBoxes } from '../Redux/DashboardSlice';
import useStyles from './Styles';


const apiCancelToken = generateCancelToken();
const api = new ApiService(apiCancelToken);

export function uuidv4() {
  return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
    var r = Math.random() * 16 | 0, v = c == 'x' ? r : (r & 0x3 | 0x8);
    return v.toString(16);
  });
}

export default function QuickAddNewBox() {
  const dispatch = useAppDispatch();
  const dashboardState = useTypedReduxSelector(state => state.dashboardPage);
  const login = useContext(AuthContext);
  const classes = useStyles();
  const theme = useTheme();
  const fullscreen = useMediaQuery(theme.breakpoints.down("sm"));

  const fabHandler = () => {
    dispatch(setAddNewBox(!dashboardState.isAddNewBox));
  }

  const onCloseHandler = () => {
    dispatch(setAddNewBox(false));
  }

  const handleAddNewBox = async () => {

    const dataSave = {
      boxToken: uuidv4(),
      boxName: dashboardState.newBoxCreator.boxName,
      boxTagName: dashboardState.newBoxCreator.boxTagName,
      boxDescription: dashboardState.newBoxCreator.boxDescription,
      boxUserId: login.userUniqueId,
    };

    try {
      dispatch(setFetchingStatus(true));
      await api.saveNewBox(dataSave);
      
      //Refreshing
      const refresh = await api.getBoxUser();
      let userData = refresh.data.data;
      dispatch(setUserBoxes(userData.boxes));

      dispatch(setFetchingState({
        message: 'New Box has been created, Congratulation!',
        severity: 'success',
        isOpen: true,
        fetchingStatus: false,
      }));

    } catch (error) {
      console.error('Add New Box',error);
      dispatch(setFetchingState({
        message: 'Failed while making box!',
        severity: 'error',
        isOpen: true,
        fetchingStatus: false,
      }));
    };
  }

  const handleFieldChange = (field, value) => {
    dispatch(setNewBoxCreator({field, value}));
  }

  return (
    <>
      <Fab onClick={fabHandler} className={classes.fabQuickAdd} aria-label="add" color="secondary">
        <AddIcons />
      </Fab>
      <Dialog onClose={onCloseHandler} open={dashboardState.isAddNewBox} fullScreen={fullscreen}>
        <DialogTitle id="responsive-dialog-title">{"Add new: Box"}</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Add new box here. Just write your project name and its description. We'll work for you to make it simple. Happy box!
          </DialogContentText>
          <form className={classes.quickAddform}>
            <Grid container spacing={3}>
              <Grid item xs={12} md={12} lg={12}>
                <TextField onBlur={event => handleFieldChange('boxName', event.target.value)} variant="outlined" id="standard-basic" label="Box Name" />
              </Grid>
              <Grid item xs={12} md={12} lg={12}>
                <TextField onBlur={event => handleFieldChange('boxTagName', event.target.value)} variant="outlined" id="standard-basic" label="Box Tag Name" />
              </Grid>
              <Grid item xs={12} md={12} lg={12}>
                <TextField onBlur={event => handleFieldChange('boxDescription', event.target.value)} variant="outlined" multiline rows={4} id="standard-basic" label="Box Description" />
              </Grid>
            </Grid>
          </form>
        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={onCloseHandler} color="primary">
            Cancel
          </Button>
          <Button onClick={() => {
            handleAddNewBox();
            onCloseHandler();
          }} color="primary" autoFocus>
            Save Box
          </Button>
        </DialogActions>
      </Dialog>
    </>
  )
}