import { FormControlLabel, Grid, Switch, Tooltip } from '@material-ui/core';
import { setWidgetOption } from '@pages/BoxPage/Redux/WidgetSlice';
import { useAppDispatch } from '@store/Store';
import React, { useState } from 'react';

export default function ButtonWidgetForm() {
  const dispatch = useAppDispatch();
  const [isToggleOptions, setToggleOptions] = useState(true);

  const handleSwitchToggle = e => {
    setToggleOptions(e.target.checked);
    dispatch(setWidgetOption({
      toggleButton: e.target.checked
    }));
  }

  return (
    <React.Fragment>
      <Grid item xs={12} md={12} lg={12}>
        <Tooltip title="If this set as true, button 
                        will use internal value (true, false) 
                        as value that will be sent to your data. Default 
                        for this options is true.
                        ">
          <FormControlLabel
            control={
              <Switch
                checked={isToggleOptions}
                onChange={handleSwitchToggle}
                name="toggleButton"
                color="primary"
              />
            }
            label="Set as toggle button"
          />
        </Tooltip>
      </Grid>
    </React.Fragment>
  );
}