import { Divider, Grid, TextField, Typography } from '@material-ui/core';
import { setBoxByFieldName } from '@pages/BoxPage/Redux/BoxSlice';
import { useAppDispatch, useTypedReduxSelector } from '@store/Store';
import React from 'react';
import useStyles from '../Styles';


/**
 * -------------
 * FormBoxName
 * -------------
 * 
 * Handle all data about box. Change or new set is come here
 * 
 * @param {any} props 
 * @returns {JSX} React.Element
 */
export default function FormBoxName(props) {
  const classes = useStyles();
  const dispatch = useAppDispatch();
  const boxState = useTypedReduxSelector(state => state.box);
  const appState = useTypedReduxSelector(state => state.app);

  const handleFieldChange = (value, stateKey) => {
    dispatch(setBoxByFieldName({
      field: stateKey,
      value: value,
    }));
  }

  return (
    <Grid container spacing={4}>
      <Grid item xs={12} md={12} lg={8}>
        <form className={classes.quickAddform}>
          <Grid container spacing={3}>
            <Grid item xs={12} md={12} lg={12}>
              <TextField 
                disabled={appState.fetchingStatus} 
                variant="outlined" 
                id="box-title" 
                label="Box Name" 
                value={boxState.boxName} 
                onChange={event => handleFieldChange(event.target.value, 'boxName')} />
            </Grid>
            <Grid item xs={12} md={12} lg={12}>
              <TextField 
                disabled={appState.fetchingStatus} 
                variant="outlined" 
                id="box-sub-title" 
                label="Box Tag Name"
                value={boxState.boxTagName} 
                onChange={event => handleFieldChange(event.target.value, 'boxTagName')} />
            </Grid>
            <Grid item xs={12} md={12} lg={12}>
              <TextField 
                disabled={appState.fetchingStatus} 
                variant="outlined" 
                multiline rows={6} 
                id="box-description" 
                label="Box Description" 
                value={boxState.boxDescription} 
                onChange={event => handleFieldChange(event.target.value, 'boxDescription')} />
            </Grid>
          </Grid>
        </form>
      </Grid>
      <Divider orientation="vertical" flexItem />
      <Grid item xs={12} md={12} lg={3}>
        <Typography variant="h6" component="h6">
          Box Name
        </Typography>
        <Typography variant="body1" component="h4" color="textSecondary">
          Write your box name there. Make some words that easy to be remembered
        </Typography>
        <br />
        <Typography variant="h6" component="h6">
          Box Description
        </Typography>
        <Typography variant="body1" component="h4" color="textSecondary">
          Describe your box here. Let them know that you could make some cool stuff with codersbox!
        </Typography>
      </Grid>
    </Grid>
  )
}