import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { WritableDraft } from "immer/dist/internal";

export type datasource = "datafield" | "manual" | "";
export type orientation = "horizontal" | "vertical" | "";

export interface WidgetSelf {
  type: string;
  variant: string;
}

export interface WidgetProperties {
  title: string;
  subtitle: string;
  metric?: string;
}

export interface WidgetOptions {
  color?: string;
  label?: string | string[];
  minValue?: number;
  maxValue?: number;
  orientation?: orientation;
  isMultiSeries?: boolean;
}

export interface WidgetLayout {
  w: number;
  h: number;
  x: number;
  y: number;
  i: string;
  minW: number;
  minH: number;
  moved: boolean;
  static: boolean;
}

export interface WidgetData {
  source: datasource;
  value: string | number | string[] | number[];
}

export interface Widget {
  data: WidgetData;
  layout: WidgetLayout;
  options: WidgetOptions;
  properties: WidgetProperties;
  self: WidgetSelf;
  widgetId: number;
}

const initialState: Widget = {
  data: {
    source: '',
    value: ''
  },
  layout: {
    x: 0,
    y: 0,
    w: 0,
    h: 0,
    minW: 2,
    minH: 9,
    i: '0',
    moved: false,
    static: false,
  },
  options: {},
  properties: {
    title: '',
    subtitle: '',
  },
  self: {
    type: '',
    variant: '',
  },
  widgetId: 0,
};

const WidgetSlice = createSlice({
  name: 'WidgetSlice',
  initialState: initialState,
  reducers: {
    setWidgetData: (state, action: PayloadAction<WidgetData>) => {
      state.data.source = action.payload.source;
      state.data.value = action.payload.value;
    },
    setWidgetDataSource: (state, action: PayloadAction<datasource>) => {
      state.data.source = action.payload;
    },
    setWidgetDataValue: (state, action: PayloadAction<string | number>) => {
      state.data.value = action.payload;
    },
    setWidgetArrayDataValue: (state, action: PayloadAction<{index: number, value: string | number}>) => {
      state.data.value[action.payload.index] = action.payload.value;
    },
    resetWidgetDataValueArray: (state) => {
      state.data.value = [];
    },
    deleteWidgetArrayDataValue: (state, action: PayloadAction<string | number>) => {
      const arrayState: string[] = state.data.value as string[];
      state.data.value = arrayState.filter(field => field !== action.payload);
    },
    setWidgetLayout: (state, action: PayloadAction<WidgetLayout>) => {
      state.layout.x = action.payload.x;
      state.layout.y = action.payload.y;
      state.layout.w = action.payload.w;
      state.layout.h = action.payload.h;
      state.layout.minW = action.payload.minW;
      state.layout.minH = action.payload.minH;
      state.layout.i = action.payload.i;
    },
    setWidgetOption: <OptionType>(state:WritableDraft<Widget>, action: PayloadAction<OptionType>) => {
      state.options = {...state.options, ...action.payload};
    },
    setWidgetArrayOption: (state, action: PayloadAction<{optionName: string; index: number; value: string | number}>) => {
      state.options[action.payload.optionName][action.payload.index] = action.payload.value;
    },
    resetWidgetOptionArray: (state, action: PayloadAction<{optionName: string;}>) => {
      state.options[action.payload.optionName] = [];
    },
    deleteWidgetArrayOption: (state, action: PayloadAction<{optionName: string; value: string | number}>) => {
      state.options[action.payload.optionName] = state.options[action.payload.optionName].filter(value => value !== action.payload.value);
    },
    setWidgetProperties: (state, action: PayloadAction<WidgetProperties>) => {
      state.properties.title = action.payload.title;
      state.properties.subtitle = action.payload.subtitle;
      state.properties.metric = action.payload.metric;
    },
    setWidgetTitle: (state, action: PayloadAction<string>) => {
      state.properties.title = action.payload;
    },
    setWidgetSubtitle: (state, action: PayloadAction<string>) => {
      state.properties.subtitle = action.payload;
    },
    setWidgetSelf: (state, action: PayloadAction<WidgetSelf>) => {
      state.self.type = action.payload.type;
      state.self.variant = action.payload.variant;
    },
    setWidgetSelfVariant: (state, action: PayloadAction<string>) => {
      state.self.variant = action.payload;
    },
    setWidgetSelfType: (state, action: PayloadAction<string>) => {
      state.self.type = action.payload;
    },
    setWidgetId: (state, action: PayloadAction<number>) => {
      state.widgetId = action.payload;
    },
    resetWidgetSlice: (state) => {
      state = initialState;
    }
  }
});

export const {
  setWidgetData,
  setWidgetDataSource,
  setWidgetDataValue,
  setWidgetArrayDataValue,
  resetWidgetDataValueArray,
  deleteWidgetArrayDataValue,
  setWidgetLayout,
  setWidgetOption,
  setWidgetArrayOption,
  resetWidgetOptionArray,
  deleteWidgetArrayOption,
  setWidgetProperties,
  setWidgetTitle,
  setWidgetSubtitle,
  setWidgetSelf,
  setWidgetId,
  setWidgetSelfVariant,
  setWidgetSelfType,
  resetWidgetSlice,
} = WidgetSlice.actions;
export default WidgetSlice.reducer;