import React, { useContext } from "react";
import {
  Switch,
  Route,
  Redirect,
} from "react-router-dom";

/** Pages */
import LoginPage from './Pages/LoginPage'
import Dashboard from './Pages/DashboardPage';
import Box from './Pages/BoxPage';
import BoxSettings from './Pages/SettingsPage';
import CodersPage from "./Pages/CodersPage";
import SignUpPage from "./Pages/SignUpPage";
import AccountPage from './Pages/AccountPage';
import { NotFound } from "./Pages/UtilPage/NotFound";

/** ICON! */
import HomeIcon from '@material-ui/icons/Home';
import BoxIcon from '@material-ui/icons/AllInbox';
import LockOpenIcon from '@material-ui/icons/LockOpen';
import CloudUploadRoundedIcon from '@material-ui/icons/CloudUploadRounded';
import AccountIcon from '@material-ui/icons/AccountCircle';
import DnsIcon from '@material-ui/icons/Dns';
import ArchiveIcon from '@material-ui/icons/Archive';

import ContainerApps from "./Container";
import { BoxStateProvider } from "@pages/BoxPage/Context/boxContextTracked";
import { AuthContext } from "@globalContext/AuthContext";
import BoxTrashPage from "@pages/BoxTrashPage";
import BoxAPIPage from "@pages/BoxAPIPage";


export const routes = [
  // Login and Auth page
  {
    path: '/login',
    name: 'Login',
    group: null,
    isPrivate: false, // For public or logged id
    isExact: true,
    isDrawerMenu: false,
    component: LoginPage,
    icon: () => <LockOpenIcon />,
  },
  {
    path: '/signup',
    name: 'SignUp',
    group: null,
    isPrivate: false,
    isExact: true,
    isDrawerMenu: false,
    component: SignUpPage,
    icon: () => <CloudUploadRoundedIcon />,
  },

  //General page
  {
    path: '/dashbox',
    name: 'Dashbox',
    group: 'Box',
    isPrivate: true,
    isExact: true,
    isDrawerMenu: true,
    component: Dashboard,
    icon: () => <HomeIcon />,
  },
  {
    path: '/boxapi',
    name: 'API',
    group: 'Box',
    isPrivate: true,
    isExact: true,
    isDrawerMenu: true,
    component: BoxAPIPage,
    icon: () => <DnsIcon />,
  },

  // {
  //   path: '/testpage',
  //   name: 'Test',
  //   group: 'Test',
  //   isPrivate: true,
  //   isExact: true,
  //   isDrawerMenu: true,
  //   component: TestPage,
  //   icon: () => <DnsIcon />,
  // },

  // Box Page
  {
    path: '/box/:uniqueBoxToken',
    name: 'Box',
    group: 'Box',
    isPrivate: true,
    isExact: true,
    isDrawerMenu: false,
    component: Box,
    icon: () => <BoxIcon />,
  },
  {
    path: '/box/:uniqueBoxToken/settings',
    name: 'Box Settings',
    group: 'Box',
    isPrivate: true,
    isExact: true,
    isDrawerMenu: false,
    component: BoxSettings,
    icon: () => null,
  },
  {
    path: '/trash',
    name: 'Box Trash',
    group: 'Box',
    isPrivate: true,
    isExact: true,
    isDrawerMenu: true,
    component: BoxTrashPage,
    icon: () => <ArchiveIcon />,
  },

  //Users Page
  {
    path: '/coders/:username',
    name: 'Coders Profile Page',
    group: 'Users',
    isPrivate: true,
    isExact: true,
    isDrawerMenu: false,
    component: CodersPage,
    icon: () => null,
  },
  {
    path: '/account',
    name: 'Account',
    group: 'Users',
    isPrivate: true,
    isExact: true,
    isDrawerMenu: true,
    component: AccountPage,
    icon: () => <AccountIcon />,
  },

  //Utils Page
  {
    path: '/lost',
    name: 'Lost Page',
    group: 'Utils',
    isPrivate: false,
    isExact: true,
    isDrawerMenu: false,
    component: NotFound,
    icon: () => null,
  },
]

export function GetRouteGroup() {
  const test = routes.filter(route => route.isDrawerMenu === true).reduce((prev, current, index) => {
    prev["groupset"] = [...prev["groupset"] || [], current.group];
    return prev;
  });
  let result = [...new Set(test.groupset)];
  return result;
}



/**
 * Route page tobe change soon
 */

export default function RoutePage() {
  const auth = useContext(AuthContext);
  return (
    <Switch>
      {routes.map((route, index) => (
        <Route key={index} exact={route.isExact} path={route.path} component={route.isPrivate ? WithLoginPage(auth.isLoggedIn)(route.component) : route.component} />
      )
      )}
      <Route path="/" exact={true} render={() => <Redirect to="/login" />} />
      <Route path="*" exact={true} render={() => <Redirect to="/lost" />} />
    </Switch>
  )
}

const WithLoginPage = loginState => WrappedPage => class withLogin extends React.Component {
 
  render() {
    return loginState ?
      // <BoxContextContainer>
      <BoxStateProvider>
        <ContainerApps>
          <WrappedPage />
        </ContainerApps>
      </BoxStateProvider>
      // </BoxContextContainer>
      : <Redirect to="/login" />;
  }
}
