import { initFirebase } from '@services/firebaseService';
import { useTypedReduxSelector } from '@store/Store';
import { useState } from 'react';
import { cdbservice } from '../BoxConnectionManager';

export default function useWidgetControlHandler(widgetType, widgetData, widgetOptions, widgetId) {
  const boxState = useTypedReduxSelector(state => state.box);
  const [newData, setNewData] = useState(0);

  const handleControl = e => {
    if (boxState.boxSettings.activeConnection === 'firebase') {

      if (widgetData.source === 'manual') {
        let updates = {};
        updates[`/control/${widgetType}${widgetId}`] = widgetData.value;
        initFirebase(null).database().ref(boxState.boxToken).update(updates);
        return;
      }

      if (widgetData.source === 'datafield') {
        let updates = {};
        updates[`/control/${widgetData.value}`] = e;
        initFirebase(null).database().ref(boxState.boxToken).update(updates);
        return;
      }
    }

    if (boxState.boxSettings.activeConnection === 'codersbox') {
      if (widgetData.source === 'datafield') {
        let updates = {};
        updates[widgetData.value] = e;
        if (cdbservice.getInstance().isConnected()) {
          console.log('Connect status = ', cdbservice.getInstance().isConnected());
          console.log('update', updates);
          setNewData(e);
          cdbservice.sendData(JSON.stringify(updates));
        } else {
          console.log('Cannot publish, not connected = ', cdbservice.getInstance().isConnected());
          cdbservice.setBoxToken(boxState.boxToken);
          cdbservice.connect();
        }
        return;
      }
    }
  }

  return { handleControl, newData };
}