import {
  Card,
  CardContent, CardHeader, Grid
} from "@material-ui/core";
import clsx from 'clsx';
import React, { useRef } from "react";
import EditWidget from "../EditWidget/EditWidget";
import useStyles from "../Styles";

export default function WidgetCardContainer(props) {
  const classess = useStyles();
  const { key } = props;
  const ref = useRef(null);

  return (
    <React.Fragment>
      <Card key={key} ref={ref} style={{ height: '100%', backgroundColor: props.widgetOptions.color}} className={clsx(classess.widgetRoot)}>
        <CardHeader title={props.widgetTitle} subheader={props.widgetSubtitle}
          action={
            <EditWidget indexLayoutWidget={props.indexWidget} idWidget={props.idWidget} title={props.widgetTitle} subtitle={props.widgetSubtitle} />
          } />
        <CardContent>
          <Grid container direction="row" justify="center" alignItems="center">
            <Grid item xs={12} md={12} lg={12}>{props.children}</Grid>
          </Grid>
        </CardContent>
      </Card>
    </React.Fragment>
  );
}

WidgetCardContainer.defaultProps = {
  widgetOptions: {}
}
