import { AppBar, IconButton, Toolbar } from '@material-ui/core';
import useScrollTrigger from '@material-ui/core/useScrollTrigger';
import MenuIcons from '@material-ui/icons/Menu';
import { setDrawerState } from '@store/AppSlice';
import { useAppDispatch, useTypedReduxSelector } from '@store/Store';
import clsx from 'clsx';
import React from 'react';
import { Link } from 'react-router-dom';
import Logo from './Logo';
import RightMenu from './RightMenu';
import useStyles from './Styles';

export default function Header(props) {
	const classes = useStyles();
	const appState = useTypedReduxSelector(state => state.app);
	const dispatch = useAppDispatch();
	const trigger = useScrollTrigger();

	return (
		<ElevationScroll {...props}>
			<AppBar
				position="sticky"
				elevation={trigger? 4: 0}
				className={clsx(classes.appBar, {
					[classes.appBarShift]: appState.isDrawerOpen,
				})}>
				<Toolbar className={clsx(classes.toolbar, props.optStyles)} variant="dense">
					<IconButton className={clsx({ [classes.appBarMenuButtonHide]: appState.isDrawerOpen })} onClick={() => dispatch(setDrawerState(true))} edge="start">
						<MenuIcons className={props.optStyles} />
					</IconButton>
					<Link to="/dashbox">
						<Logo onClick={(e) => console.log(e)} logoType="white" />
					</Link>
					{/* <Search /> */}
					<div className={classes.grow} />
					<RightMenu />
				</Toolbar>
			</AppBar>
		</ElevationScroll>
	);
}

function ElevationScroll(props) {
	const { children, window } = props;
	// Note that you normally won't need to set the window ref as useScrollTrigger
	// will default to window.
	// This is only being set here because the demo is in an iframe.
	const trigger = useScrollTrigger({
		disableHysteresis: true,
		threshold: 0,
		target: window ? window() : undefined,
	});
	// console.log(trigger,children,window);
	return React.cloneElement(children, {
		elevation: trigger ? 4 : 0,
	});
}