import { useTrackedSelector } from "@store/Store";
import React, { useEffect, useState } from "react";
import ReactApexChart from "react-apexcharts";
import { WidgetProps } from "../widgetPropsInterface";

export default function ChartRadialComponent({
  widgetId,
  widgetData,
  widgetOptions,
  widgetProperties
}: WidgetProps) {
  const widgetDataState = useTrackedSelector().widgetData;
  const [chartOptions] = useState({
    chart: {
      id: "realtimeRadialChart",
      height: 200,
      type: "radialBar",
    },
    plotOptions: {
      radialBar: {
        dataLabels: {
          value: {
            show: true,
            fontSize: "14px",
            formatter: function (val) {
              return `${val} ${widgetProperties?.metric}`;
            },
          },
        },
        hollow: {
          size: "60%",
        },
      },
    },
    labels: [widgetOptions?.label],
  });

  const [data, setData] = useState(() => {
    if (
      localStorage.getItem(
        `lastDataChartCircular${widgetOptions?.label}-${widgetId}`
      ) !== "undefined" &&
      localStorage.getItem(
        `lastDataChartCircular${widgetOptions?.label}-${widgetId}`
      ) !== null
    ) {
      return [
        (Number(
          localStorage.getItem(
            `lastDataChartCircular${widgetOptions?.label}-${widgetId}`
          )
        ) /
          Number(widgetOptions?.maxValue)) *
          100,
      ];
    }
    return [0];
  });

  useEffect(() => {
    let chartData = 0;
    if (widgetData.source === "manual") {
      chartData =
        (Number(widgetData.value) / Number(widgetOptions.maxValue)) * 100;
      setData([Number(chartData.toFixed(1))]);
    }

    if (
      widgetData.source === "datafield" &&
      Object.keys(widgetDataState.widgetDataContainer).length !== 0
    ) {
      chartData =
        (widgetDataState.widgetDataContainer[String(widgetData.value)] /
          Number(widgetOptions?.maxValue)) *
        100;
      setData([Number(chartData.toFixed(1))]);
    }

    /** Set Last Data */
    localStorage.setItem(
      `lastDataChartCircular${widgetOptions?.label}-${widgetId}`,
      String(chartData)
    );

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [widgetDataState.widgetDataContainer]);

  return (
    <React.Fragment>
      <ReactApexChart
        options={chartOptions}
        series={[data]}
        type="radialBar"
        height={250}
      />
    </React.Fragment>
  );
}
