import { useAppDispatch } from "@store/Store";
import React, { useEffect } from "react";
import BoxConnectionManager from "./BoxConnectionManager";
import AddWidget from "./Components/AddWidget/AddWidget";
import BoxContainer from "./Components/boxContainer";
//Icons
import EditWidgetDrawer from "./Components/EditWidget/EditWidgetDrawer";
import WidgetContainer from "./Components/WidgetContainer/WidgetContainer";
import { resetBoxSlice } from "./Redux/BoxSlice";




export default function Box() {
  const dispatch = useAppDispatch();

  useEffect(() => {
    return () => dispatch(resetBoxSlice());
  }, []);

  return (
    <React.Fragment>
      <BoxContainer>
        <BoxConnectionManager />
        <WidgetContainer />
        <AddWidget />
        <EditWidgetDrawer />
      </BoxContainer>
    </React.Fragment>
  );
}